import Vue from 'vue';
import Vuex from 'vuex'
import auth from './modules/auth';
import theme from './modules/theme';
import modal from './modules/modal';
import state from './modules/state';
import tree from './modules/tree';
import layouts from './modules/layouts';
import bankNet from './modules/bankNet';
import axios from 'axios'
import createPersistedState from "vuex-persistedstate";
import iziToast from 'izitoast';

Vue.use(Vuex);
export const store = new Vuex.Store({
    modules: {
        auth,
        theme,
        modal,
        bankNet,
        layouts,
        state,
        tree
    },
    state: {
        user_interface: {},
        menu_open: true,
        lang: 'ru',
        svgStore: {},
        paramCard: {
            // group_l:true,
        },
        formParameters: {},
        paramDataCard: {},
        faqid: false,
        content: '',
        asyncReports: false,
        currentTabPage: 0,
        adding_elems_link: '', //ссылка для перехода на пустую карточку
        adding_dependency_field: null, // айди родительского обьекта при создании пустой карточки
        notifications: [], //массив пользовательских уведомлений
        bookmarks: [] //массив пользовательских закладок
        // validators:[]
    },
    getters: {
        isMenuOpen: state => {
            return state.menu_open;
        },
        formParameters: state => {
            return state.formParameters;
        },
        getLang: state => {
            return state.lang;
        },
        getSvgStore: state => {
            return state.svgStore;
        },
        getTreeAction: state => {
            return state;
        },
        getParamCard: state => {
            return state.paramCard;
        },
        getParamDataCard: state => {
            return state.paramDataCard;
        },
        getUserInterface: state => {
            return state.user_interface;
        },

        getAsyncReports: state => {
            return state.asyncReports;
        },
        getTabIndex: state => {
            return state.currentTabPage;
        },
        getModalHtml: state => {
            return state.modal.html;
        },

        getNotifications: state => {
            return state.notifications;
        },
        getFaqid: state => {
            return state.faqid;
        },
        getBookmarks: state => {
            return state.bookmarks;
        }
    },
    actions: {
        showToast({commit, state}, payload) {
            let template = payload.template ? payload.template : 'success';
            let title = payload.title ? payload.title : '';
            let message = payload.message ? payload.message : '';
            let position = payload.position ? payload.position : 'topRight';

            if (title.length === 0 && message.length === 0) return false;

            iziToast[template]({
                title: title,
                message: message,
                position: position,
            });
        },

        asyncReports(context) {
            context.commit('asyncReports');
        },
        ["ParamDataCard"]: function ({commit}, data) {
            commit("setParamDataCard", data);
        },
        ["setSvgStore"]: function ({commit}, data) {
            return commit("setSvgStore", data);
        }
    },
    mutations: {
        toggleSidebar(state) {
            state.menu_open = !state.menu_open;
        },
        formParameters(state, formParameters) {
            state.formParameters = formParameters;
        },

        stateTreeActions(state, stateTreeActions) {
            state.stateTreeActions = stateTreeActions;
        },

        defaultState(state, info) {
            state.modal.html = '';
            state.modal.editorContent = '';
            state.modal.showModal = false;
            state.modal.modalType = '';
            state.modal.submitModal = undefined;
            state.modal.texts = {};
            // state.user_verified = false;
            // state.verification_required = false;
            state.token_1c = null;
        },

        // showModal(state, info) {
        //     state.modal.html = info.html;
        //     state.modal.showModal = true;
        //     state.modal.modalType = info.type;
        //     if (info.type == 'infoModal' || info.type == 'confirmModal' || info.type == 'editorModal' || info.type === 'documentModal') {
        //         state.modal.texts = info.texts;
        //     }
        //     if (info.type === 'editorModal') {
        //         state.modal.editor.value = info.editor.val;
        //         state.modal.editor.model = info.editor.model;
        //         state.modal.editor.name = info.editor.name;
        //     }
        // },
        // hideModal(state) {
        //     state.modal.showModal = false;
        //     if (state.modal.modalType == 'confirmModal') {
        //         state.modal.submitModal = false;
        //     }
        // },
        // submitModal(state) {
        //     console.log("MODAL submitted")
        //     state.modal.submitModal = true;
        // },
        // setModalResponse(state,data) {
        //     state.modal.response = data;
        // },

        changeTab(state, tab) {
            state.currentTabPage = tab;
            // console.log(state.currentTabPage);
        },
        // changeAddingLink(state, link) {
        //     state.adding_elems_link = link;
        // },
        // changeAddingDependencyField(state, field) {
        //     state.adding_dependency_field = field;
        // },
        asyncReports(state) {
            state.asyncReports = !state.asyncReports;
        },
        addNotification(state, notification) {
            state.notification = notification;
        },
        deleteNotification(state, link) {

            state.notifications.forEach((notification, index) => {
                if (notification.link == link) {
                    state.notifications.splice(index, 1);
                }
            })
        },

        // validateChildren(state, result){
        //     state.validators.push(result);
        // },
        setParamCard(state, data) {
            state.paramCard = {
                // group_l:true
            };
            Object.keys(data).forEach((key) => {
                state.paramCard[key] = data[key];
            });
        },
        setParamDataCard(state, data) {
            console.log(typeof data, data, data.length);
            if (Object.keys(data).length > 0) {
                Object.keys(data).forEach((key) => {
                    state.paramDataCard[key] = data[key];
                });
            } else {
                state.paramDataCard = {};
            }

        },
        setSvgStore(state, data) {
            if (Object.keys(data).length > 0) {
                Object.keys(data).forEach((key) => {
                    state.svgStore[key] = data[key];
                });
            } else {
                state.svgStore = {};
            }

        },
        setFaqid(state, id) {
            state.faqid = id
        },
        setUserInterface(state, data) {
            state.user_interface = data;
        },
        setLang(state, lang) {
            localStorage.setItem('lang', lang);
            state.lang = lang;
        },
        setBookmarks(state, data) {
            state.bookmarks = data;
        }
    },
    plugins: [createPersistedState()]
});
