import {Validator} from 'vee-validate';

const maxValueWithSpaces = (value, [max]) => {
    return parseInt(value.replace(/\s+/gi, '')) <= Number(max);
};

const minValueWithSpaces = (value, [max]) => {
    return parseInt(value.replace(/\s+/gi, '')) >= Number(max);
};

const integerWithSpaces = (value) => {
    return Number.isInteger(parseInt(value.replace(/\s+/gi, '')));
};

Validator.extend('max_value_with_spaces', maxValueWithSpaces);
Validator.extend('min_value_with_spaces', minValueWithSpaces);
Validator.extend('integer_with_spaces', integerWithSpaces);

