<template>
    <div id="app">
	    <transition-group name="fade" mode="in-out">
		    <div class="spinner" v-show="isLoading" key="spinner">
			    <atom-spinner
				    :animation-duration="1000"
				    color="var(--accent)"
			    />
		    </div>
            <div v-show="!isLoading" key="content">
                <component :is="layout"></component>
            </div>
        </transition-group>

    </div>
</template>

<script>
	import {EventBus} from './main'
    import {mapGetters} from 'vuex';
    import cabinet from "./layouts/cabinet/cabinet";
    import bankNet from "./layouts/bankNet/bankNet";

    import {AtomSpinner} from 'epic-spinners';


    export default {
        name: 'App',
        components: {
            AtomSpinner,
            cabinet,
            bankNet,
        },
        computed: {
            layout(){
                return this.$store.getters.layout
            }
        },
        data: function () {
            return {
                isLoading:     true,
            }
        },
		async created(){
			await this.$store.dispatch('checkTheme');
			EventBus.$on('changeTheme', () => {
				this.isLoading = true;
				setTimeout(()=>{this.isLoading = false},1000)
			})
		},
        async mounted() {
            this.$nextTick(e => {
                setTimeout(e=>{this.isLoading = false;},1000)
            })
        }
    };
</script>

