export const bookmark = {
    data() {
        return {
            bookmark: null
        }
    },
    computed: {
        isBookmarked() {
            return !!this.bookmark;
        }
    },
    methods: {


        /**
         * Проверка на наличие пользовательских закладок на данную страницу
         */
        checkIfBookmarked() {
            this.bookmark = this.$store.getters.getBookmarks.find(bookmark => {
                return bookmark.bookmark_url === this.$bRoute.fullPath;
            });
        },

        /**
         *
         * @param bookmark {Object}
         * @returns {Promise<void>}
         */
        async deleteBookmark(bookmark) {
            try {
                this.bookmark = null;
                let res = await this.$http.post('/admin/delete/bookmark', bookmark);

                this.$store.commit('setBookmarks', res.data.bookmarks);

                this.$store.dispatch('showToast', {
                    template: res.data.template ? res.data.template : 'success',
                    title: res.data.title || ' ',
                    message: res.data.message,
                    position: res.data.position || 'topRight',
                })
            } catch (e) {
                console.error(e);
                this.bookmark = bookmark;
            }
        },

        async addBookmark(element_name) {
            try {
                this.bookmark = {
                    bookmark_url: this.$bRoute.fullPath,
                    bookmark_representation:element_name
                };
                var res = await this.$http.post('/admin/add/bookmark', {
                    bookmark_url: this.$bRoute.fullPath,
                    bookmark_representation: element_name
                });
                this.$store.commit('setBookmarks', res.data.bookmarks);

                this.$store.dispatch('showToast', {
                    template: res.data.template ? res.data.template : 'success',
                    title: res.data.title || ' ',
                    message: res.data.message,
                    position: res.data.position || 'topRight',
                })

            } catch (e) {
                console.error(e);
                this.$store.dispatch('showToast', {
                    template:'error',
                    title:  ' ',
                    message: e.toString(),
                    position: 'topRight',
                });
                debugger;
                this.bookmark = null;
            }
        }
    }
};