export const dragAndDrop = {
    data: function () {
        return {
            dragDropEnable: false,
            dragSettings: {
                typeTree: false,
                selectorLine: 'tr',
                selectorContainer: '#list_table',
                selectorBox: 'tr',
            },
            dragLines: [],
            dragContainer: null,
            dragDrop: {
                item: {},
                index: 0,
                line: null,
                newIndex: 0,
                depth: 0,
            },
            dragDropInitialization: false,
        }
    },
    methods:{
        recountLine(listItems){
            let lineN = 0;
            let items = [];
            listItems = listItems.filter(e=>e).map((current,index,array)=>{
                if(current.status === 3) return current
                lineN++;
                if(current.line_n !== lineN){
                    current.line_n = lineN;
                    current.status = 1;
                }
                return current
            });
        },
        dragAndDropInit(selectorLines,selectorTable,type,selectorBox){
            this.dragDropEnable = !this.dragDropEnable;
            if(!this.dragDropInitialization){
                this.dragDropInitialization = true;
                this.dragSettings.typeTree = type;
                this.dragSettings.selectorLine = selectorLines;
                this.dragSettings.selectorContainer = selectorTable;
                this.dragSettings.selectorBox = selectorBox;
                this.dragLines = document.querySelectorAll(selectorLines);
                this.dragContainer = document.querySelector(selectorTable);
                this.dragAndDrop();

            }
        },
        dragAndDrop(){
            let self = this;
            /*
            * Добавление события клика на Lines
            * */
            [...this.dragLines].forEach((line)=>line.addEventListener("mousedown",function (event) {

                let clickCollapse = event.target.closest('.collapse-item');
                // Проверка на активированый режим
                if(self.dragDropEnable === false || clickCollapse) return false;

                let container = line.closest(self.dragSettings.selectorContainer);
                let lines = container.children;

                let listItems = itemsModel();

                let index = [...lines].indexOf(this.closest(self.dragSettings.selectorBox));

                // Разделители
                let linesSeparators = [...lines].map((tr)=>tr.offsetTop);
                linesSeparators.unshift(0);
                let moveElementTr = true;

                self.dragDrop.index = index;
                self.dragDrop.line = line;

                let coords = getCoords(line);
                let shiftX = event.pageX - coords.left;
                let shiftY = event.pageY - coords.top;


                let shadowDomTr = createShadowElem();
                shadowDomTr.style.left = event.pageX - shiftX + 'px';
                shadowDomTr.style.top =  (event.pageY - shiftY) - window.scrollY + 'px';

                const timerHandler = throttled(100,moveMouse);
                document.addEventListener("mousemove", timerHandler);

                document.addEventListener("mouseup", function() {
                    moveElement();
                    document.removeEventListener("mousemove", timerHandler);
                });

                /*
                * Поиск название модели
                * */
                function itemChildModels(depth,itemParent,item){
                    if(itemParent === null) return false;
                    let modelKeys = self.$store.getters.getDepthParameters(depth)['children_models'];
                    let modelKey = '';
                    modelKeys.forEach(model => {

                        if (itemParent.hasOwnProperty(model) &&
                            itemParent[model].length > 0 &&
                            itemParent[model].indexOf(item) !== -1)
                            modelKey = model;
                    });

                    return modelKey;
                }

                /*
                * Модель []
                * */
                function itemsModel(){
                    if(!self.dragSettings.typeTree) return self.listItemsStash;
                    let keyModel = itemChildModels(self.dragDrop.depth,self.dragDrop.item.parent,self.dragDrop.item);
                    return self.dragDrop.item.parent !== null ? self.dragDrop.item.parent[keyModel] : self.items
                }

                /*
                * Создание и настройка теневого элемента
                * */
                function createShadowElem(){
                    let shadowTr = line.cloneNode(true);
                    !self.dragSettings.typeTree ?
                        [...lines[0].children].forEach((elem,indexElem)=>
                            shadowTr.children[indexElem].style.width = elem.offsetWidth + 'px'
                        ) : false;
                    shadowTr.style.position = 'fixed';
                    shadowTr.style.zIndex = 1000;
                    shadowTr.style.width = line.offsetWidth + 'px';
                    shadowTr.style.transition = '.1s linear';
                    shadowTr.style.opacity = '0.6';

                    return  container.appendChild(shadowTr);
                }

                /*
                * Получение координат элемента
                * */
                function getCoords(elem) {
                    let box = elem.getBoundingClientRect();
                    return {
                        top: box.top + pageYOffset,
                        left: box.left + pageXOffset
                    };
                }

                /*
                * Тормозящий  декоратор
                * */
                function throttled(delay, fn) {
                    let lastCall = 0;
                    return function (...args) {
                        const now = (new Date).getTime();
                        if (now - lastCall < delay) {
                            return;
                        }
                        lastCall = now;
                        return fn(...args);
                    }
                }

                /*
                * Подстветка линии для переноса
                * */
                function backlightLines(index = null){
                    let trsLines = [...lines];
                    self.dragSettings.typeTree === false ?
                        trsLines.unshift(document.querySelector('thead tr')) :
                        false;
                    trsLines.forEach(tr=>tr.classList.remove('dragSelect'));
                    if(index === null) return false;
                    let trLine = index === 0 ? trsLines[0] :  trsLines[index];
                    trLine ? trLine.classList.add('dragSelect') : false;
                }

                /*
                * Перенос элемента массива
                * */
                function array_move(arr, old_index, new_index) {
                    if (new_index >= arr.length) {
                        var k = new_index - arr.length + 1;
                        while (k--) {
                            arr.push(undefined);
                        }
                    }
                    arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
                    return arr;
                };

                /*
                * Минимальное близкое и максимально близкое число
                * */
                function minMaxCoincidence(number){
                    var closestLeft,
                        closestRight,
                        current;
                    for (var i = 0; i < linesSeparators.length; i++) {
                        current = linesSeparators[i];
                        if (current < number && (typeof closestLeft === 'undefined' || closestLeft < current)) {
                            closestLeft = current;
                        } else if (current > number && (typeof closestRight === 'undefined' || closestRight > current)) {
                            closestRight = current;
                        }
                    }
                    return {closestLeft,closestRight}
                }

                /*
                * Обработка движения мыши
                * */
                function moveMouse(ev) {
                    if(moveElementTr === false) return false;
                    let {closestLeft,closestRight} = minMaxCoincidence(getCoords(shadowDomTr).top - getCoords(container).top)
                    let index = linesSeparators.indexOf(closestLeft);
                    index === -1 ? index  = linesSeparators.indexOf(closestRight) : false;
                    backlightLines(index);
                    self.dragDrop.newIndex = index;

                    shadowDomTr.style.left = ev.pageX - shiftX + 'px';
                    shadowDomTr.style.top = (ev.pageY - shiftY) - window.scrollY + 'px';
                }

                /*
                * Перемещение элемента после отжатия кнопки
                * */
                function moveElement() {
                    if(!moveElementTr) return false;
                    moveElementTr = false;
                    shadowDomTr.remove();
                    backlightLines();
                    if(self.dragDrop.index === self.dragDrop.newIndex) return false;
                    array_move(listItems,self.dragDrop.index,self.dragDrop.newIndex);
                    self.recountLine(listItems);
                }
            }));

        },
    }
}
