<template>
    <div class="verify">
        <div class="container">
            <div><h1>{{form_parameters.form_title}}</h1></div>
            <Zone v-if="inputs"
                  :models="{Consumers:main_data_models[model_name]}"
                  :fields="inputs"
                  :model_name="model_name"
                  class="left-zone full-width"
            ></Zone>
            <div class="col d-flex justify-content-end page_buttons_bottom_new"
                 v-if="actions">
                <a
                        v-for="action in actions"
                        :id="action.code"
                        :class="action.class"
                        @click="saveModel(action)">
                    {{action.title}}
                </a>
            </div>

        </div>
    </div>
</template>

<script>
    import Zone from "./Zone";
    import axios from 'axios'

    export default {
        name: "Verify",

        data() {
            return {
                main_data_models: null,
                inputs: null,
                model_name: null,
                actions: null,
                form_parameters: null
            }
        },
        props: {
            type: {
                default: null
            },
        },
        methods: {
            async saveModel({code, link}) {

                if (code === 'cancel')
                {
                    this.$store.commit('logoutUser');
                    this.$emit('verificationFailed',{})
                }

                var result = await this.$validator.validateAll();
                if (result) {

                    for (var item in this.fieldsVV) {
                        this.fieldsVV[item].touched = true;
                    }

                    let data = {
                        ...this.main_data_models[this.form_parameters.form_base_data_model][0],
                        token_1c: this.$store.getters.getUser1cToken,
                        reset_token: this.$store.getters.getUserResetToken
                    };

                    try {
                        var res;

                        if(this.type==='reset_password'){
                            res = await axios.post( process.env.VUE_APP_BASE_URL+link, data);
                        }
                        else{
                            // res = await this.$http.post('/admin/verification/inputs', data)
                            res = await this.$http.post(link, data)
                        }
                        for (var item in this.fieldsVV) {
                            this.fieldsVV[item].touched = false;
                        }
                        // this.$store.state.user_verified = true;
                        // this.$store.state.verification_required = false;
                        this.$emit('verificationSubmitted',{})


                    } catch (error) {
                        this.$store.dispatch('showToast',{
                            template:  'error',
                            title:     error.response.status,
                            message:   error.response.data.message,
                            position:  error.response.data.position,
                        })
                    }



                    //
                    // await http.post(link, data)
                    //     .then(async res => {
                    //         for (var item in this.fieldsVV) {
                    //             this.fieldsVV[item].touched = false;
                    //         }
                    //         // this.$store.state.user_verified = true;
                    //         // this.$store.state.verification_required = false;
                    //         this.$emit('verificationSubmitted',{})
                    //     })
                    //     .catch(error => {
                    //         this.$toast.error(error.response.data.message, '' + error.response.status, {position: 'topRight'})
                    //     });

//
                }
                else {
                    for (var item in this.fieldsVV) {
                        this.fieldsVV[item].touched = true;
                    }
                    this.$toast.error('Некорректные данные', '', {position: 'topRight'})
                }
            }
        },

        async mounted() {
            try {
                var res;

                if(this.type==='reset_password'){
                    res = await axios.post( process.env.VUE_APP_BASE_URL+'/verification/inputs', {type: this.type});
                }
                else{
                    res = await this.$http.post('/admin/verification/inputs', {type: this.type})
                }
                // res = this.type === 'reset_password' ? await axios.post('http://192.168.1.121/api/verification/inputs', {type: this.type}) : this.$http.post('/admin/verification/inputs', {type: this.type});

                this.inputs = res.data.inputs;

                this.main_data_models = res.data.main_data_models;

                this.form_parameters = res.data.form_parameters;


                this.actions = res.data.actions;

                this.model_name = this.form_parameters.form_base_data_model;
            } catch (error) {
                await this.$store.dispatch('showToast', {
                    template: 'error',
                    title: error.response.status,
                    message: error.response.data.message,
                    position: error.response.data.position,
                })
            }


        },

        components: {
            Zone
        }

    }
</script>

<style lang="scss">
    @import "../assets/sass/variables.scss";
    @import "../assets/sass/mixin.scss";


    .full-width {
        min-width: 100%;
    }
</style>
