import {EventBus} from "../../main";

const state = {
    modal: {
        visible: false,
        modal_title: null,
        fields: null,
        link: null,
        buttons: null,
        request: true,
        type: '',
        model: '',
        addModels: '',
    }
};


const mutations = {
    showModal(state, data) {
        state.modal.modal_title = data.modal.modal_title;
        state.modal.fields = data.modal.inputs;
        state.modal.link = data.modal.link;
        state.modal.buttons = data.modal.buttons;
        state.modal.type = data.modal.type;
        state.modal.model = data.modal.model;
        state.modal.addModels = data.modal.addModels;
        state.modal.request = data.modal.request;
        data.root.$bvModal.show('b-modal');
    },
    hideModal(state, data) {
        data.root.$bvModal.hide('b-modal');
        EventBus.$emit('editFinish');
        console.log('hidden')
    },

    destroyModal(state) {
        state.modal.modal_title = null;
        state.modal.fields = null;
        state.modal.link = null;
        state.modal.buttons = null;
    },

    // handleModalOk(state, data) {
    //     data.e.preventDefault();
    //     // debugger;
    //     data.root.$nextTick( async() => {
    //         await this.commit("hideModal", {root: data.root});
    //         // this.commit('destroyModal')
    //     })
    //     // e.vueTarget.hide();
    // }
};

const getters = {
    getModalTitle: state => state.modal.title,
    getModalFields:state=>state.modal.fields,
    getModalButtons:state=>state.modal.buttons,
    getModal:state=>state.modal,
    getModalModel:state=>state.modal.model,
    getModalAddModels:state=>state.modal.addModels

};

export default {
    state,
    mutations,
    getters
}
