import { EventBus } from '../../main'

const state = {
    matchLeft: null,
    matchRigth: null,
    getCalculateData: {direction: 'from', value: null},
    currencies: [],
};


const mutations = {
    refreshMatchLeft(state, match) {
        state.matchLeft = match;
    },
    refreshMatchRigth(state, match) {
        state.matchRigth = match;
    },
    refreshCalculate(state, data) {
        state.getCalculateData.value = data.value;
        state.getCalculateData.direction = data.direction;
    },
    updateCurrencies(state, currencies){
        state.currencies = currencies;
    }
};

const actions = {
    updateCurrencies(state,currencies){
        state.commit('updateCurrencies', currencies);
    },
    refreshMatch(state,match){
        state.commit('refreshMatchLeft', match.left);
        state.commit('refreshMatchRigth', match.right);
    },
    refreshCalculate(state,data){
        state.commit('refreshCalculate',data);
    }
}

const getters = {
    getMatchCurrencies: state => {
        return {
            left: state.matchLeft,
            right: state.matchRigth,
        };
    },
    getCalculateData: state => {
        return state.getCalculateData;
    },
    getCurrencies: state => {
        return state.currencies;
    }
}

export default {
    state,
    mutations,
    actions,
    getters
}