    }
    &:hover,
    &.editCell {
      &:after {
        opacity: 1;
      }
    }
    &.editCell {
      .editCellList {
        position: relative;
        .undo {
          position: absolute;
          top: 50%;
          right: 5px;
          cursor: pointer;
          transform: translateY(-50%);
          &:hover {
            i {
              color: $accent;
            }
          }
        }
      }
    }
    .select__input-hidden {
      position: absolute;
      left: -100%;
    }
  }
  .error {
    position: absolute;
    top: 1px;
    left: 1px;
    cursor: help;
    font-size: 13px;
    color: $invalid;
  }
  tr {
    &:hover {
      transform: none !important;
      box-shadow: none !important;
    }
  }
