<template>
  <div class="mt-3 mb-3">
    <label class="label-large">Калькулятор</label>
    <form @submit.prevent="calculateHandler">
      <div class="radiobuttons">
        <p class="radiobuttons__item">
          <label for="from_radio">
            Отдадите
            <input
              class="radiobuttons__input"
              value="from"
              type="radio"
              id="from_radio"
              v-model="direction"
            />
            <span class="radiobuttons__span"></span>
          </label>
          <label for="to_radio">
            Получите
            <input
              class="radiobuttons__input"
              value="to"
              type="radio"
              id="to_radio"
              v-model="direction"
            />
            <span class="radiobuttons__span"></span>
          </label>
        </p>
      </div>
      <div class="field-box w-auto d-inline mr-3">
        <input type="text" class="w-auto" required v-model="value" />
      </div>
      <b class="mr-3">{{currencyCalculate}}</b>
      <input class="btn btn-green btn-small" type="submit" value="Рассчитать" />
    </form>
  </div>
</template>

<script>
import vSelect from "vue-select";
import { EventBus } from "../../main";
export default {
  name: "CalculateCurrencies",
  data() {
    return {
      direction: "from",
      value: "",
      isActive: false
    };
  },
  mounted() {
    EventBus.$on("refreshMatchItems", currencies => {
      this.currencies = currencies;
    });
  },
  computed: {
    currencies() {
      return this.$store.getters.getCurrencies;
    },
    currencyCalculate() {
      if (this.currencies.length === 0) return "Currencies not found";

      let math =
        this.direction === "from"
          ? this.$store.getters.getMatchCurrencies.left
          : this.$store.getters.getMatchCurrencies.right;

      let currency = this.currencies.find(curr => curr.id === math);

      return currency.title;
    }
  },
  methods: {
    async calculateHandler($event) {
      this.isActive = true;
      let data = {
        direction: this.direction,
        value: this.value
      };
      await this.$store.dispatch("refreshCalculate", data);
      this.$emit("calculateCurrenciesUp", data);
    }
  },
  components: {
    vSelect
  }
};
</script>

<style lang="scss" scoped>
.radiobuttons {
  margin-top: 0;
  &__item {
    flex-direction: column;
    align-items: flex-start;
    label {
      &:first-child {
        margin-bottom: 0.5rem;
      }
    }
  }
}
form {
  display: flex;
  align-items: center;
}
.field-box input {
  margin-top: 0;
}
</style>