function stringToPath(string) {
  let rePropName = /[^.[\]]+|\[(?:(-?\d+(?:\.\d+)?)|(["'])((?:(?!\2)[^\\]|\\.)*?)\2)\]|(?=(?:\.|\[\])(?:\.|\[\]|$))/g;
  let reEscapeChar = /\\(\\)?/g;
  var result = [];
  string.replace(rePropName, function (match, number, quote, string) {
    result.push(quote ? string.replace(reEscapeChar, "$1") : number || match);
  });
  return result;
}
function getProp(obj, keys, defaultValue) {
  keys = Array.isArray(keys) ? keys : stringToPath(keys);
  // keys = keys.filter(e=>!Number.isInteger(+e));
  obj = obj[keys[0]];
  if (obj && keys.length > 1) {
    return getProp(obj, keys.slice(1));
  }
  return obj === undefined ? defaultValue : obj;
}

function install(Vue) {
  Vue.exFilter = new exFilter();
  Vue.prototype.$exFilter = new exFilter();
  Vue.component("filter-element", {
    methods: {
      toggleAll() {
        this.$emit('toggleSelected')
      },
      sortingChanged(e) {
        let sortBy = e.target.dataset["key"];
        let sortDesc = e.target.dataset["direction"];
        this.$emit("sortingChanged", { sortBy, sortDesc });
      },
      async filter(value, key, select) {
        let items = await this.$exFilter.filter(value, key, select);
        this.$emit("refreshItems", items);
      },
      async exFilterCondition(
        optionKey,
        fieldKey,
        dependent,
        option,
        value,
        format
      ) {
        let items = await this.$exFilter.exFilterCondition(
          optionKey,
          fieldKey,
          dependent,
          option,
          value,
          format
        );
        this.$emit("refreshItems", items);
      },
      showDropdown(e) {
        let widthHtml = document.documentElement.clientWidth;
        let leftElement = e.target.getBoundingClientRect().left;
        let widthDropdown =
          parseInt(window.getComputedStyle(document.documentElement).fontSize) *
          30;
        if (widthHtml <= leftElement + widthDropdown)
          e.target.closest("th").classList.add("right");
        e.target.closest("th").classList.toggle("open");
      },
      stopClick(e) {
        e.preventDefault();
        e.stopPropagation();
      }
    },
    render(h) {
      if (this.field.key === 'actions') {
        return (<div class="list_checkbox checkbox checkbox-all">
          <label for="checkbox-all">
            <input
              type="checkbox"
              id="checkbox-all"
              onClick={this.toggleAll}
            />
            <span class="checkbox-custom">
              <i class="fa fa-check"></i>
            </span>
          </label>
        </div >);
      }
      else if (!this.exFilterOn) {
        return (
          <div
            class={{
              table__thead__overflow: !this.form_parameters
                .list_header_break_line,
              table_thead_break_line: this.form_parameters
                .list_header_break_line,
              table_thead_vertical_line: this.field.vertical_line,
            }}
          >
            {this.field.label}
          </div>
        );
      } else {
        return (
          <div class={{ open: this.exFilterOn, dropdown: true }}>
            <button class="btn btn-dropdown" onClick={this.showDropdown}>
              {this.field.label}
            </button>
            <img class="drop-i" src="/img/dropdown-i.png" />
            <div class="dropdown_box dropdown_box_select exFilterDropdown" onClick={this.stopClick}>
              <div class="exFilterSorts">
                <button
                  data-key={this.field.key}
                  data-direction={false}
                  onClick={this.sortingChanged}
                  class="dropwond_link"
                >
                  {this.$exFilter.translate["SortAZ"]}
                </button>
                <button
                  data-key={this.field.key}
                  data-direction={true}
                  onClick={this.sortingChanged}
                  class="dropwond_link"
                >
                  {this.$exFilter.translate["SortZA"]}
                </button>
              </div>
              <filter-сondition
                propsField={this.field}
                propsType={this.type}
                onExFilterList={({ key, value, select }) =>
                  this.filter(value, key, select)
                }
                onExFilterCondition={({
                  optionKey,
                  fieldKey,
                  dependent,
                  option,
                  value,
                  format,
                }) =>
                  this.exFilterCondition(
                    optionKey,
                    fieldKey,
                    dependent,
                    option,
                    value,
                    format
                  )
                }
              />
              <filter-value
                propsField={this.field}
                propsType={this.type}
                onExFilterList={({ key, value, select }) =>
                  this.filter(value, key, select)
                }
              />
            </div>
          </div>
        );
      }
    },
    props: {
      form_parameters: {
        type: Object,
        required: true,
      },
      exFilterOn: {
        type: Boolean,
        required: true,
      },
      field: {
        type: Object,
        required: true,
      },
      type: {
        type: String,
        required: true,
      },
    },
  });

  Vue.component("filter-value", {
    methods: {
      toggleSection(e) {
        let elemToggle = e.target
          .closest(".exFilterBox")
          .querySelector(".exFilterBody");
        new this.$toggler(elemToggle).toggle();
      },
      removeDuplicate(arr) {
        if (typeof arr[0] !== "undefined" && arr[0].hasOwnProperty("value"))
          return this.removeDuplicateObject(arr, "value");
        return arr.filter(
          (item, pos, self) =>
            self.indexOf(this.$exFilter.propDeep(item)) === pos
        );
      },
      removeDuplicateObject(array, field) {
        if (array[0] === "undefined") return false;
        return array.filter((obj, pos, arr) => {
          return arr.map((mapObj) => mapObj[field]).indexOf(obj[field]) === pos;
        });
      },
      emptyRules() {
        if (this.field.rules.length === 0) return true;
        if (
          this.field.rules.length ===
          this.removeDuplicate(this.field.itemsAll).length
        )
          return false;
        return null;
      },
      async chooseAll() {
        let res = await this.emptyRules();
        if (res !== null && res === true) return false;
        this.$emit("exFilterList", {
          value: "e",
          key: this.field.key,
          select: true,
        });
      },
      async removeAll() {
        let res = await this.emptyRules();
        if (res !== null && res === false) return false;
        this.$emit("exFilterList", {
          value: "e",
          key: this.field.key,
          select: false,
        });
      },
    },
    render(h) {
      return (
        <div class="exFilterValue exFilterBox">
          <div class="exFilterHead" onClick={this.toggleSection}>
            <p>{this.$exFilter.translate["FilterValue"]}</p>
          </div>
          <div class="exFilterBody active">
            <ul class={this.field.key}>
              <a class="exFilter-a" href={"#"} onClick={this.chooseAll}>
                {this.$exFilter.translate["SelectAll"]}
              </a>
              <a class="exFilter-a" href={"#"} onClick={this.removeAll}>
                {this.$exFilter.translate["DeselectAll"]}
              </a>
              {this.removeDuplicate(this.field.itemsAll).map((option) => {
                let optionVal = this.$exFilter.propDeep(option);
                return (
                  <li
                    class={{ selected: !this.field.rules.includes(optionVal) }}
                  >
                    <button
                      onClick={() =>
                        this.$emit("exFilterList", {
                          value: optionVal,
                          key: this.field.key,
                        })
                      }
                      class="dropwond_link text__overflow"
                    >
                      {" "}
                      {optionVal.length !== 0
                        ? optionVal
                        : this.$exFilter.translate["Empty"]}
                    </button>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
      );
    },
    props: {
      field: {
        type: Object,
        required: true,
      },
      type: {
        type: String,
        required: true,
      },
    },
  });

  Vue.component("filter-сondition", {
    methods: {
      toggleSection(e) {
        let elemToggle = e.target
          .closest(".exFilterBox")
          .querySelector(".exFilterBody");
        let res = elemToggle === true;
        new this.$toggler(elemToggle).toggle();
      },
      removeDuplicate(arr) {
        return arr.filter((item, pos, self) => self.indexOf(item) === pos);
      },
      removeDuplicateObject(array, field) {
        if (array[0] === undefined) return false;
        return array.filter((obj, pos, arr) => {
          return arr.map((mapObj) => mapObj[field]).indexOf(obj[field]) === pos;
        });
      },
      select(option) {
        if (!option || option.key === "notchosed") {
          this.option = this.$exFilter.conditions[0];
          this.$emit("exFilterCondition", {
            optionKey: this.option.key,
            fieldKey: this.field.key,
            dependent: false,
            option: this.option,
            value: "",
          });
          return;
        }
        if (option.key === "notfilled" || option.key === "filled") {
          this.$emit("exFilterCondition", {
            optionKey: option.key,
            fieldKey: this.field.key,
            dependent: false,
            option: option,
            value: "",
          });
        }
        this.option = option;
      },
    },
    components: {
      Datepicker,
      "v-select": vSelect,
    },
    data() {
      return {
        option: this.$exFilter.conditions[0],
      };
    },
    render(h) {
      return (
        <div class="exFilterСondition active exFilterBox">
          <div class="exFilterHead" onClick={this.toggleSection}>
            <p>{this.$exFilter.translate["FilterCondition"]}</p>
          </div>
          <div class="exFilterBody">
            <v-select
              value={this.option.label}
              on-input={this.select}
              propsOptions={this.$exFilter.conditions}
            />
            {this.option.typeDependent === "text" && (
              <input
                type="text"
                placeholder={this.option.placeholder}
                on-input={() =>
                  this.$emit("exFilterCondition", {
                    optionKey: this.option.key,
                    fieldKey: this.field.key,
                    dependent: false,
                    option: this.option,
                    value: event.target.value,
                  })
                }
              />
            )}
            {this.option.typeDependent === "range" && (
              <form>
                <input
                  type="text"
                  placeholder={this.option.placeholder[0]}
                  on-input={() =>
                    this.$emit("exFilterCondition", {
                      optionKey: this.option.key,
                      fieldKey: this.field.key,
                      dependent: false,
                      option: this.option,
                      value: event.target,
                    })
                  }
                />

                <input
                  type="text"
                  placeholder={this.option.placeholder[1]}
                  on-input={() =>
                    this.$emit("exFilterCondition", {
                      optionKey: this.option.key,
                      fieldKey: this.field.key,
                      dependent: false,
                      option: this.option,
                      value: event.target,
                    })
                  }
                />
              </form>
            )}
            {this.option.typeDependent === "date" && (
              <datepicker
                format="dd-MM-yyyy"
                placeholder={this.option.placeholder}
                onSelected={(e) =>
                  this.$emit("exFilterCondition", {
                    optionKey: this.option.key,
                    fieldKey: this.field.key,
                    dependent: false,
                    option: this.option,
                    value: e,
                    format: this.field.format,
                  })
                }
              />
            )}
          </div>
        </div>
      );
    },
    props: {
      field: {
        type: Object,
        required: true,
      },
      type: {
        type: String,
        required: true,
      },
    },
  });
}
exFilter.install = install;
exFilter.version = "0.3";

if (window.Vue) {
  window.Vue.use(exFilter);
}
