<template>
  <div
    class="content"
    :class="{'active':$store.state.menu_open,'nav-md':$store.state.menu_open,'nav-sm':!$store.state.menu_open}"
  >
    <Sidebar v-if="user_verified && sidebarData" :sidebarData="sidebarData"></Sidebar>
    <div id="main" :class="{'active':$store.state.menu_open}">
      <div class="wrapper" v-if="user_verified">
        <Header
          :notifications="notifications"
          :user="user"
          :userActions="userActions"
          :translations="header_translations"
          :user_interfaces="user_interfaces"
        ></Header>
        <Menu v-if="topMenuData.items" :menu_data="topMenuData"></Menu>
        <Breadcrumbs></Breadcrumbs>

        <div class="main__content">
          <transition name="fade" mode="out-in">
            <router-view :key="$bRoute.matched&& $bRoute.matched[0] && $bRoute.matched[0].path "></router-view>
          </transition>
        </div>
      </div>
      <Verify
        v-if="verification_required"
        @verificationSubmitted="verification_required = false; user_verified=true"
      ></Verify>
      <!--            <modal v-if="getModalState"></modal>-->
      <!--            <Modaldynamic ref="dmodal"></Modaldynamic>-->

      <Footer v-if="user_verified"></Footer>
      <BModal></BModal>
    </div>
  </div>
</template>

<script>
import Header from "./Header.vue";
import Sidebar from "./Sidebar.vue";
import Footer from "./Footer.vue";
import Verify from "./Verify.vue";
import Menu from "./Menu.vue";
import Breadcrumbs from "./Breadcrumbs";
import { AtomSpinner } from "epic-spinners";
import { mapGetters } from "vuex";
import BModal from "./BModal";

const Home = resolve => {
  require.ensure(["./Home.vue"], () => {
    resolve(require("./Home.vue"));
  });
};
const List = resolve => {
  require.ensure(["./List.vue"], () => {
    resolve(require("./List.vue"));
  });
};
const Card = resolve => {
  require.ensure(["./Card.vue"], () => {
    resolve(require("./Card.vue"));
  });
};
const Report = resolve => {
  require.ensure(["./Report.vue"], () => {
    resolve(require("./Report.vue"));
  });
};
const Profile = resolve => {
  require.ensure(["./Profile.vue"], () => {
    resolve(require("./Profile.vue"));
  });
};
const Error = resolve => {
  require.ensure(["./Error.vue"], () => {
    resolve(require("./Error.vue"));
  });
};
const Document = resolve => {
  require.ensure(["./Document.vue"], () => {
    resolve(require("./Document.vue"));
  });
};
const TextPage = resolve => {
  require.ensure(["./TextPage.vue"], () => {
    resolve(require("./TextPage.vue"));
  });
};
const Steps = resolve => {
  require.ensure(["./Steps.vue"], () => {
    resolve(require("./Steps.vue"));
  });
};
const Info = resolve => {
  require.ensure(["./Info.vue"], () => {
    resolve(require("./Info.vue"));
  });
};
const Faq = resolve => {
  require.ensure(["./Faq.vue"], () => {
    resolve(require("./Faq.vue"));
  });
};
const AttachedFiles = resolve => {
  require.ensure(["./AttachedFiles.vue"], () => {
    resolve(require("./AttachedFiles.vue"));
  });
};
const RequestCard = resolve => {
  require.ensure(["./RequestCard.vue"], () => {
    resolve(require("./RequestCard.vue"));
  });
};
const Tree = resolve => {
  require.ensure(["./Tree.vue"], () => {
    resolve(require("./Tree.vue"));
  });
};
const Questionnaire = resolve => {
  require.ensure(["./Questionnaire.vue"], () => {
    resolve(require("./Questionnaire.vue"));
  });
};

export default {
  name: "app",
  components: {
    List,
    Home,
    Card,
    Report,
    Profile,
    Error,
    Document,
    TextPage,
    Steps,
    Info,
    Header,
    Sidebar,
    Footer,
    // Modal,
    // Modaldynamic,
    Menu,
    Breadcrumbs,
    AtomSpinner,
    Faq,
    AttachedFiles,
    RequestCard,
    Verify,
    BModal,
    Tree,
    Questionnaire
  },
  data: function() {
    return {
      headerRefresh: true,
      modal: true,
      key: 0,
      // userVerify: false,
      currentComponent: "",
      routes: [],
      userActions: [],
      header_translations: {},
      user: false,
      notifications: [],
      user_interfaces: [],
      router: {},
      loading: false,
      sidebarData: [],
      topMenuData: [],
      // changePasswordModal:null
      user_verified: false,
      verification_required: false
    };
  },
  computed: {
    modalState() {
      console.log(this.getModalState);
    },
    ...mapGetters([
      // 'getModalState',
      // 'getModalType',
      // 'getModalTexts',
      // 'getModalHtml',
      // 'getModalEditorContent',
      // 'getModalResponse',
      // 'verificationRequired'
    ]),
    getNotifications() {
      return this.$store.getters.notifications;
    }

    // verificationRequired() {
    //     return this.user.verification_required;
    // }
  },
  watch: {
    "$bRoute.path": function(old) {
      this.routeChange();
    },
    getNotifications: {
      handler: function(value) {
        this.notifications = value;
      },
      deep: true
    }
  },
  methods: {
    async getMenu(type, interface_id) {
      try {
        let res = await this.$http.post(
          "/admin/menu?mode=0&module=main&object=mainmenu&menutype=" +
            type +
            "&interface_id=" +
            interface_id
        );
        type === "left"
          ? (this.sidebarData = res.data)
          : (this.topMenuData = res.data);
        await this.$store.dispatch("showToast", {
          template: res.data.template,
          title: res.data.title,
          message: res.data.message,
          position: res.data.position
        });
      } catch (error) {
        await this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    },
    theme() {},
    routerReady() {
      this.$nextTick(() => {
        if (!this.$bRoute.matched[0]) this.$bRouter.replace("/404"); // Роут не найден
      });
    },
    async routeChange() {
      let currentPath, routes, result, replace;
      currentPath = this.$bRoute.path; // Текущий url
      routes = this.routes; // Все роуты из backend
      if (routes.length === 0) return false;
      replace = routes.length === this.$bRouter.options.routes.length;
      result = await this.$bRouter.matcher.matchBackRoutes(
        currentPath,
        routes,
        !replace
      ); // Поиск роута по url
      this.$bRouter.onReady(this.routerReady);
    },
    async getRouters() {
      try {
        const res = await this.$http.post("/admin/FeRoutes/index", null);
        this.routes = res.data;
      } catch (error) {
        await this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    },
    // async checkChangePassword() {
    //     await this.$refs.dmodal.showmodal(this.changePasswordModal);
    //     let result = this.getModalResponse;
    //     this.$store.commit('setModalResponse', '');
    //     if (result.status !== 0) {
    //         localStorage.removeItem('token');
    //         this.$store.commit('logoutUser');
    //         this.$http.defaults.headers.Authorization = null;
    //         this.$bRouter.push('/login');
    //     }
    //     this.userVerify = true;
    //
    // },
    async getProfileInfo() {
      let userInterface =
        this.$store.getters.getUserInterface &&
        this.$store.getters.getUserInterface.id
          ? this.$store.getters.getUserInterface.id
          : null;
      await this.$http
        .post(
          "/admin/menu?mode=0&module=main&object=profilemenu&interface_id=" +
            userInterface,
          null
        )
        .then(res => {
          this.$store.dispatch("changeTimeout", res.data.localStorageTimeout);
          this.userActions = res.data.actions;
          this.user = res.data.user;
          if (Object.keys(this.$store.getters.getUserInterface).length === 0)
            this.$store.commit("setUserInterface", res.data.user_interfaces[0]);
          this.user_interfaces = res.data.user_interfaces;
          this.$store.commit("addNotification", res.data.notifications);
          this.$store.commit("setBookmarks", res.data.bookmarks);
          this.notifications = res.data.notifications;
          this.header_translations = res.data.translations;

          res.data.use_home_page === "0" && res.data.default_url
            ? this.$store.commit("default_url", res.data.default_url)
            : false;

          if (res.data.user.verification_required) {
            this.verification_required = true;
          } else {
            this.user_verified = true;
          }
        });
    }
  },
  notificationSystem: {
    options: {
      ballon: {
        balloon: true,
        position: "topRight"
      },
      info: {
        position: "topRight"
      },
      success: {
        position: "topRight"
      },
      warning: {
        position: "topRight"
      },
      error: {
        position: "topRight"
      }
    }
  },
  async created() {
    global.main = this;
    this.$http.defaults.headers.Authorization =
      "Bearer " + this.$store.getters.token;
    // Дефолтное значение Modal vuex
    this.$store.commit("defaultState");
    // Проверка темы
    this.theme();
    // Получение Header
    await this.getProfileInfo();
    // Получение роутов через API
    await this.getRouters();
    // Вызов метода парса роутов
    await this.routeChange();
    // Получение Sidebar JSON
    await this.getMenu("left", this.$store.getters.getUserInterface.id);
    // Получение TopMenu JSON
    // await  this.getMenu('top'); // todo Получение данных для topmenu
  }
};
</script>

<style lang="scss">
#main {
  min-height: 100vh;
  flex-basis: 100%;
}

.fade-transition {
  transition: opacity 0.2s ease;
}

.fade-enter,
.fade-leave {
  opacity: 0;
}

.switch-transition {
  transition: all 0.5s ease-in-out;
}

.switch-enter {
  left: 100%;
}

.switch-leave {
  left: -100%;
}
</style>
