<template>
  <div id="bankNet">
    <router-view v-if="$bRoute.path === '/login'"></router-view>
    <template name="bankNet-Monitor" v-else>
      <div class="content">
        <div id="main">
          <div class="wrapper">
            <Header
              :user="{}"
              :notifications="notifications"
              :userActions="userActions"
              :translations="header_translations"
              :user_interfaces="user_interfaces"
            ></Header>
            <div class="container">
              <div class="row">
                <div class="col-md-4">
                  <Rate></Rate>
                </div>
                <div class="col">
                  <Menu v-if="topMenuData.items" :menu_data="topMenuData"></Menu>
                  <div class="main__content">
                    <Monitor></Monitor>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Footer></Footer>
          <BModal></BModal>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { AtomSpinner } from "epic-spinners";
import Header from "../../components/Header.vue";
import Sidebar from "../../components/Sidebar.vue";
import Footer from "../../components/Footer.vue";
import Rate from "../../components/Rate.vue";
import Auth from "../../components/Auth.vue";
import Menu from "../../components/Menu.vue";
import Breadcrumbs from "../../components/Breadcrumbs";
const Monitor = resolve => {
  require.ensure(["../../components/Monitor.vue"], () => {
    resolve(require("../../components/Monitor.vue"));
  });
};
export default {
  name: "bankNet",
  data() {
    return {
      currentComponent: "",
      routes: [],
      userActions: [],
      header_translations: {},
      notifications: [],
      user_interfaces: [],
      router: {},
      loading: false,
      sidebarData: [],
      topMenuData: {
        items: [
          {
            img: "/img/interfacedashboard/file.svg",
            depth: 1,
            link: "/",
            group_l: false,
            separator: "10",
            title: "Обменики"
          },
          {
            img: "/img/interfacedashboard/file.svg",
            depth: 1,
            link: "/faq",
            group_l: false,
            separator: "10",
            title: "FAQ"
          },
          {
            img: "/img/interfacedashboard/file.svg",
            depth: 1,
            link: "/contacts",
            group_l: false,
            separator: "10",
            title: "Контакты"
          }
        ],
        menu_parameters: {
          first_level_icons: true,
          any_level_icons: true,
          empty_title: "Пусто"
        }
      }
    };
  },
  methods: {
    async getMenu(type, interface_id) {
      try {
        let res = await this.$http.post(
          "/menu/bank/net?mode=0&module=main&object=mainmenu&menutype=" +
            type +
            "&interface_id=" +
            interface_id
        );
        type === "left"
          ? (this.sidebarData = res.data)
          : (this.topMenuData = res.data);
        await this.$store.dispatch("showToast", {
          template: res.data.template,
          title: res.data.title,
          message: res.data.message,
          position: res.data.position
        });
      } catch (error) {
        await this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    },
    routerReady() {
      this.$nextTick(() => {
        if (!this.$bRoute.matched[0]) this.$bRouter.replace("/404"); // Роут не найден
        // let XML = this.getRoutesXML();
        // console.log(XML);
      });
    },
    async routeChange() {
      let currentPath, routes, result, replace;
      currentPath = this.$bRoute.path; // Текущий url
      routes = this.routes; // Все роуты из backend
      if (routes.length === 0) return false;
      replace = routes.length === this.$bRouter.options.routes.length;
      result = await this.$bRouter.matcher.matchBackRoutes(
        currentPath,
        routes,
        !replace
      ); // Поиск роута по url
      if (this.$bRoute.meta.reset_vuex) {
        this.$store.dispatch("refreshMatch", { left: 0, rigth: 0 });
        this.$store.dispatch("refreshCalculate", {
          value: null,
          direction: "from"
        });
      }
      if (
        this.$bRoute.meta.matchRequired &&
        (!this.$store.getters.getMatchCurrencies.left ||
          !this.$store.getters.getMatchCurrencies.left)
      ) {
        this.$bRouter.push("/");
      }
      this.$bRouter.onReady(this.routerReady);
    },
    async getRouters() {
      this.routes = [
        {
          path: "/faq",
          component: "Faq",
          name: "Faq",
          meta: {
            route: "/faq/index/public"
          }
        },
        {
          path: "/",
          component: "List",
          name: "Monitor",
          meta: {
            route: "/bn/exchange/offers/list",
            reset_vuex: true
          }
        },
        {
          path: "/contacts",
          component: "Feedback",
          name: "Feedback",
          meta: {
            route: "/bank/net/contact/card"
          }
        },
        {
          path: "/offers",
          component: "List",
          name: "List",
          meta: {
            route: "/bn/exchange/offers/list",
            matchRequired: true
          }
        },
        {
          path: "/404",
          component: "Error",
          name: "Ошибка"
        },
        {
          path: "/405",
          component: "Error",
          name: "Ошибка"
        },
        {
          path: "/500",
          component: "Error",
          name: "Ошибка"
        },
        {
          path: "/login",
          component: "Auth",
          name: "Auth",
          meta: {
            route: "/admin/faq/index/public"
          }
        }
      ];
    },
    getRoutesXML() {
      function getRoutesList(routes, pre) {
        return routes.reduce((array, route) => {
          const path = `${pre}${route.path}`;

          if (route.path !== "*") {
            array.push(path);
          }

          if (route.children) {
            array.push(...getRoutesList(route.children, `${path}/`));
          }

          return array;
        }, []);
      }
      const list = getRoutesList(
        this.$bRouter.options.routes,
        "http://dev.banknet.club"
      )
        .map(route => `<url><loc>${route}</loc></url>`)
        .join("\r\n");
      return `<urlset xmlns="http://www.sitemaps.org/schemas/sitemap/0.9" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xsi:schemaLocation="http://www.sitemaps.org/schemas/sitemap/0.9 http://www.sitemaps.org/schemas/sitemap/0.9/sitemap.xsd">
        ${list}
      </urlset>`;
    }
  },
  async created() {
    this.$store.dispatch("refreshMatch", { left: 0, right: 0 });
    this.$store.dispatch("refreshCalculate", {
      value: null,
      direction: "from"
    });
    // Получение роутов через API
    await this.getRouters();
    // Вызов метода парса роутов
    await this.routeChange();
    await this.getMenu("bankNet", this.$store.getters.getUserInterface.id);
  },
  watch: {
    "$bRoute.path": function(old) {
      this.routeChange();
    }
  },
  components: {
    Header,
    Sidebar,
    Rate,
    Monitor,
    Auth,
    Footer,
    Menu,
    Breadcrumbs,
    AtomSpinner
  }
};
</script>

