#bankNet {
  table {
    thead tr {
      border-radius: 15px;
      overflow: hidden;
    }
  }
  #menu {
    padding-left: 10px;
    margin-top: 20px;
    border: 1px solid #e4e7ea;
  }
  #menu .navTop__inner .navTop__link {
    padding-left: 10px;
  }
  #header #logo {
    margin-left: 0;
    transform: scale(1);
    height: 7rem;
    display: flex;
    align-items: center;
    padding-left: 15px;
    img {
      max-height: 50px;
    }
  }
}
</style>
