const state = {
    list: {},
    card:{},
    localTimeout: 0,
    lastPage: {
        old: '/',
        new: '/',
    },
};

const actions  = {
    listState(state,payload){
       this.commit('saveList',payload);
    },
    cardState(state,payload){
       this.commit('saveCard',payload);
    },
    changeTimeout(state,value){
        this.commit('changeTimeout',value)
    },
    lastPage(state,page){
        this.commit('lastPage',page)
    },
};

const mutations = {
    removeListPage(state,page){
        delete state.list[page];
    },
    removeCardPage(state,page){
        delete state.card[page];
    },
    lastPage(state,page){
        state.lastPage.old = state.lastPage.new;
        state.lastPage.new = page;
    },
    saveList(state,payload){
        let newList = state.list[payload.page] = {};
        newList.currentPage = payload.currentPage;
        newList.scroll = payload.scroll;
        newList.perPage = payload.perPage;
    },
    saveCard(state,payload){
        let newCard = state.card[payload.page] = {};
        newCard.models = payload.models;
    },
    changeTimeout(state,value){
        state.localTimeout = value;
    }
};

const getters = {
    localTimeout: state => state.localTimeout,
    lastPage: state => state.lastPage,
    listsList: state => state.list,
    cardsList: state => state.card,
};

export default {
    state,
    mutations,
    getters,
    actions
}
