import {EventBus} from '../main.js';
import {inputHandler} from "./inputHandler";

export const editorTable = {
    data: function () {
        return {
            // Editor
            editor: false,
            editorMode: '',
            editorCell: {
                field: {},
                item:  null,
                td: {},
                oldValue: '',
            },
            editorItem: {
                item: {},
                index: 0,
                id: 0,
            },
            editorActions: ['addRow','removeRow','editRow','changeOrder']
        }
    },
    mixins: [inputHandler],
    watch: {
      editorMode(){
          this.initialEditor(this.editorMode);
      },
    },
    methods:{
        formatFields(){
            let fieldsEdit = this.fields.filter((field,index )=>{
                return field.dependent_field === 'AccessRole'
            })
            fieldsEdit = [...fieldsEdit];
            return fieldsEdit;
        },
        /**
         * Пересчёт line_n
         */
        recountLine(){
            let lineN = 0;
            let items = [];
            items = this.listItemsStash.filter(e=>e).map((current,index,array)=>{
                if(current.status === 3) return current
                lineN++;
                if(current.line_n !== lineN){
                    current.line_n = lineN;
                    this.setStatusLine(current,1);
                }
                return current
            });
            this.refreshItems(items);
        },
        /**
         * Очистка зависимого select
         * @param {Object} direction Направление
         */
        async changeOrder(direction){
            let self = this;
            if(!this.editorItem.item) return false;
            let currentLineN = this.editorItem.item.line_n;
            let searchLineN = direction ? +currentLineN-- : +currentLineN++;
            if(currentLineN === 0) return false;
            let rowSelect = function(index){
                const selectedRows =  [];
                selectedRows[index] = true;
                self.$refs.table.selectedRows = selectedRows;
            }
            let swapLineN = async function (array,f,l) {
                let temp = array[f].line_n;
                array[f].line_n = array[l].line_n;
                array[l].line_n = temp;
                array[f].status = 1;
                array[l].status = 1;
                await self.sortingChanged({sortBy: 'line_n', sortDesc: false});
                rowSelect(l)
            }
            this.listItemsStash.forEach((item,index)=>{
                if(item.line_n === searchLineN){
                    let directionIndex = direction ? index - 1 : index + 1;
                    if(typeof this.listItemsStash[directionIndex] === 'undefined') return false;
                    swapLineN(this.listItemsStash,index,directionIndex);
                }
            });
        },
        /**
         * Обработчик
         */
        handlerEditor(){
            if(!this.block_parameters.edit_values) return false;
            this.editorMode = this.block_parameters.edit_mode ? this.block_parameters.edit_mode : null;
            this.editorActions = this.block_parameters.editor_actions || this.editorActions;
            this.editor === true ? this.destroyEditor() : this.initialEditor(this.block_parameters.edit_mode);
        },
        /**
         * Отключение
         */
        destroyEditor(){
            this.editor = false;
            document.documentElement.removeEventListener('click', this.checkEditorState);
        },
        /**
         * Инициализация
         */
        
        initialEditor(mode){
            if(!this.block_parameters.edit_values) return false;
            this.editor = true;
            this.$nextTick(()=>{
                if(mode === 'inline')
                    document.documentElement.addEventListener('click', this.checkEditorState);
            })
            setClassColumn.call(this);
            function setClassColumn() {
                let editableColumn = [];
                let rows = document.querySelectorAll('#' + this.listName + ' tbody tr');
                this.fields.forEach((elem,index)=>{
                    if(elem.edit) editableColumn.push(index);
                });
                let tdsEditable = [...rows].map(row=>
                    [...row.children].filter((td,tdIndex)=>editableColumn.includes(tdIndex))
                );
                [].concat(...tdsEditable).forEach(td=>td.classList.add('editable'))
            }
        },
        validateSelect(field,deep = true){
            if( this.editorCell.field.validation &&
                this.editorCell.field.validation.required)  {
                let value = this.editorCell.item[field.key];

                if(!value) {
                    this.$validator.errors.add({
                        field: field.key,
                        msg: 'Поле  обязательно для заполнения.',
                        scope: this.editorCell.index,
                        id: this.editorCell.index+field.key,
                    })
                }else{
                    this.$validator.errors.removeById(this.editorCell.index+field.key)

                }
                if(field.dependent_data && field.dependent_data.supreme && deep) this.validateSelect(this.fields.filter(e=>e.key===field.dependent_data.supreme_field_key)[0])
                return !!value
            }
        },

        /**
         * Обработка клика на странице
         * @param {Event} e
         */
        async checkEditorState(e){
            // this.$nextTick(async ()=>{
            let clickTable = e.target.closest('table');
            if(!clickTable && this.editorCell.item){
                let res = await this.validationHandler();

                if(!res) return false
                if(this.editorCell.oldValue !== (this.editorCell.item && this.editorCell.item[this.editorCell.field.key]))
                    this.saveEditState();
                let tdEdit =  document.querySelectorAll('td.editCell');
                [...tdEdit].forEach(td=>td.classList.remove('editCell'));
                this.editorCell.field = '';
                this.editorCell.item = null;
                this.editorCell.td = '';
                this.editorCell.oldValue = '';
                this.editorCell.index = null;
            }
            // })
        },
        /**
         * Удаление строки
         */
        removeLine(item){
            let itemRow = Object.keys(item).length > 1 ? item : this.editorItem.item;
            if(itemRow.id === null){
                this.model.splice(this.editorItem.index,1)
            }else{
                this.model.filter(e=>e)
            }
            itemRow.status = 3;
            this.recountLine();
        },
        /**
         * Редактирование строки через попап
         */
        editLine(item){
            let itemRow = Object.keys(item).length > 1 ? item : this.editorItem.item;
            if(itemRow.id === undefined) return false;
            let self = this;
            let data = {modal:{
                    modal_title: this.block.block_title,
                    fields: this.fields,
                    type: 'editRow',
                    model: Object.assign({},itemRow),
                    addModels: this.add_models,
                    request: false,
                    block: this.block,
                }}
            EventBus.$emit('showModal',data)
            EventBus.$on('editFinish', (data) => {
                if(data){
                    for(var key in itemRow)
                        itemRow[key] = data[key];
                    this.setStatusLine(itemRow,1);
                    self.refreshTable();
                }
                EventBus.$off('editFinish')
            })
        },
        /**
         * Изменение статуса строки
         * @param {Object} item
         * @param {Number} status
         */
        setStatusLine(item,status){
            // Если у item нет id,тогда не изменять статус
            if(item.id === null) return item;
            item.status = status;
            return item;
        },
        /**
         * Добавление новой строки в Model
         */
        async addLine(){
            let self = this;
            function emptyRow(){
                let newRow = {...self.block.block_parameters.empty_row};
                newRow.line_n = (self.listItemsStash[self.listItemsStash.length-1] && self.listItemsStash[self.listItemsStash.length-1].line_n + 1) || 1;
                return newRow;
            }
            let newObject = emptyRow();
            let type = this.block_parameters.edit_mode === 'addRow' ? 'addRowWithRequest' : 'editRow';
            let request = this.block_parameters.edit_mode === 'addRow';
            let link = '/admin/'+this.block_parameters.write_link || null;
            await this.sortingChanged({sortBy: 'line_n', sortDesc: false});
            newObject = this.setStatusLine(newObject,2);
            let data = {modal:{
                    modal_title: this.block.block_title,
                    fields: this.fields,
                    type,
                    model: newObject,
                    addModels: this.add_models,
                    model_name: this.block_model,
                    request,
                    link,
                    block: this.block,
                }}
            EventBus.$emit('showModal',data)
            EventBus.$on('editFinish', (data) => {
                if(data){
                    data.status = 2;
                    this.model.push(data);
                    this.recountLine();
                    self.refreshTable();
                }
                EventBus.$off('editFinish')
            })

        },

        /**
         * Сохранение введёных данных
         */
            saveEditState(){
            this.resetDependentSelect(this.editorCell.field,this.editorCell.item,this.fields)
            if(this.block_parameters.choose_first_option &&  this.editorCell.field && this.editorCell.field.dependent_data.supreme_field_key){
                this.chooseFirstOption(this.editorCell.field,this.editorCell.item);
            }
            if(this.block_model === 'AccessRightByRole'){
                let isKey = this.editorCell.item.hasOwnProperty(this.editorCell.field.key+'_key')
                if(!isKey) this.editorCell.item[this.editorCell.field.key+'_key'] = null;
            }
            this.errorsInput = false;
            if(this.editorCell.item !== null)
                this.setStatusLine(this.editorCell.item,1);
        },
        /**
         * Откат измененией
         */
        undoChanges(){
            this.editorCellData.item[this.editorCellData.field.key] = this.editorCellData.oldValue;
            this.editorCellData.field = '';
            this.editorCellData.item = null;
            this.editorCellData.td = '';
            this.editorCellData.oldValue = '';
            this.errorsInput = false;
        },
        async validationHandler(){
            let result = true;

            if( this.editorCell.field.key && this.editorCell.field.validation){

                if(this.editorCell.field.type === 'select' || this.editorCell.field.type === 'date'){
                    await this.validateSelect(this.editorCell.field);
                }else{
                    result =  await this.$validator.validate(this.editorCell.index+'.'+this.editorCell.field.key);
                }
            }
            return result;
        },
        /**
         * Редактирование ячейки
         * @param {string} item
         * @param {Object} field
         * @param {Element} td
         */
        async enableEditorCell(item,field,td){
            if(field.edit === false) return false;

            let res = await this.validationHandler();

            if(!res) return false
            if(this.editorCell.item && this.editorCell.td){
                this.saveEditState();
            }
            try{
                let tdEdit =  document.querySelectorAll('td.editCell');
                [...tdEdit].forEach(td=>td.classList.remove('editCell'));
                let index = [...this.items].findIndex(
                    (itemCurrent,index,arr)=>{
                        return this.$isEqual(itemCurrent,item)
                    },
                    this);
                this.$validator.attach({name: field.key,scope: index, rules: field.validation})
                this.editorCell.field = field;
                this.editorCell.item = item;
                this.editorCell.td = td;
                this.editorCell.oldValue = item[field.key];
                this.editorCell.td.classList.add('editCell');
                this.editorCell.index = index;
                this.refreshTable();
            }
            catch (e) {
                console.error(e);
            }
        },
    },
    computed: {
        editorRowSelect(){
            return this.editor && this.editorMode === 'row';
        },
        editorInline(){
            return this.editor && this.editorMode === 'inline';
        },
    }
}
