<script>
import { EventBus } from "../main";

export default {
  data() {
    return {
      userDropdown: false,
      langDropdown: false,
      user_photo: "url(/img/user_default.png)"
    };
  },
  props: {
    notifications: {
      type: Array,
      default: []
    },
    user_interfaces: {
      type: Array,
      default: []
    },
    userActions: {
      type: Array,
      default: []
    },
    user: {
      type: Object,
      default: {}
    },
    translations: {
      type: Object,
      default: {}
    }
  },
  mounted() {
    /**
     * установка фото пользователя в шапке(пока не используется)
     */
    if (this.user.user_photo)
      this.user.user_photo = "url(" + this.user.user_photo + ")";
    EventBus.$on("userPhotoChanged", data => {
      this.user.user_photo = data.src;
    });
  },
  beforeDestroy() {
    /**
     * установка фото пользователя в шапке(пока не используется)
     */
    EventBus.$off("userPhotoChanged");
  },
  methods: {
    switchLayout(layout) {
      this.$store.commit("switchLayout", layout);
    },
    logout() {
      localStorage.removeItem("token");
      this.$store.commit("logoutUser");
      this.$http.defaults.headers.Authorization = null;
      if (this.$isBankNet) {
        this.$store.commit("switchLayout", "bankNet");
      }
      this.$bRouter.push("/");
    },
    handleResizeHeader(e) {
      e.stopPropagation();
      this.$store.state.menu_open = !this.$store.state.menu_open;
      return;
      if (localStorage.getItem("menuOpen")) {
        localStorage.setItem("menuOpen", this.$store.state.menu_open);
      } else {
        localStorage.setItem("menuOpen", this.$store.state.menu_open);
      }
    },
    async changeLang(lang) {
      try {
        this.$store.commit("setLang", lang);
        var res = await this.$http.post("language/set/public", { lang });
        this.$bRouter.go(0);
      } catch (error) {
        await this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    },
    cssPath(el) {
      if (!(el instanceof Element)) return;
      var parent = document.getElementById("sidebar"),
        path = [];
      while (el.nodeType === Node.ELEMENT_NODE && el != parent) {
        var selector = el.nodeName.toLowerCase();
        if (el.id) {
          selector += "#" + el.id;
        } else {
          var sib = el,
            nth = 1;
          while (
            sib.nodeType === Node.ELEMENT_NODE &&
            (sib = sib.previousSibling) &&
            nth++
          );
          selector += ":nth-child(" + nth + ")";
        }
        path.unshift(selector);
        el = el.parentNode;
      }
      return path.join(" > ");
    },
    changeStyle(code) {
      this.$store.dispatch("changeTheme", { code });
    },

    userInterfaceUpdated(e) {
      if (e.interface.id === this.$store.getters.getUserInterface.id) return;
      this.$store.commit("setUserInterface", e.interface);
      this.$bRouter.go(0);
    }
  }
};
</script>

