import Vue from 'vue';

const def = Vue.component('languageSelect', {
    name: 'languageSelect',
    template: `<div class="header_lang">
              <ul class="d-flex">
                <li
                  class="btn btn-link btn-small"
                  :class="{'active':$store.getters.getLang==='ru' }"
                  @click="$emit('changeLang','ru')"
                >
                  <a>RU</a>
                </li>
                <li
                  class="btn btn-link btn-small"
                  :class="{'active':$store.getters.getLang==='en' }"
                  @click="$emit('changeLang','en')"
                >
                  <a>EN</a>
                </li>
              </ul>
            </div>`,
});
const dropdown = Vue.component('languageSelect', {
    name: 'languageSelect',
    template: `<div class="header__lang">
                <div class="btn-dropdown dropdown-toggler d-flex align-items-center">
                    <div class="mr-4">
                        <img src="/img/interfacedashboard/language.svg" class="svg">
                    </div>
                    <img src="/img/dropdown-i.png" alt="">
                </div>
                <div class="dropdown_box">
                    <ul>
                        <li>
                            <span @click="$emit('changeLang','ru')">RU</span>
                        </li>
                        <li>
                            <span @click="$emit('changeLang','en')">EN</span>
                        </li>
                    </ul>
                </div>
            </div>`,
});

export default {
    def,
    dropdown
}
