import {EventBus} from '../../main'

const state = {
    token: localStorage.getItem('token') || '',
    home_url: '/',
    token_1c: null,
    reset_token: null,
};


const mutations = {
    default_url(state, home_url) {
        state.home_url = home_url
    },
    loginUser(state, token) {
        state.token = token
        EventBus.$emit('tokenRefresh',token)
    },
    addUser1cToken(state, token_1c) {
        state.token_1c = token_1c;
    },
    addUserResetToken(state, reset_token) {
        state.reset_token = reset_token;
    },
    logoutUser(state) {
        state.token = ''
    }
};

const getters = {
    isLoggedIn: state => {
        return !!state.token;
    },
    // isVerified:state=>{
    //   return state.user_verified;
    // },
    // verificationRequired:state=>{
    //   return state.verification_required;
    // },
    home_url: state => {
        return state.home_url;
    },
    token: state => {
        return state.token;
    },
    getUser1cToken: state => {
        return state.token_1c
    },
    getUserResetToken: state => {
        return state.reset_token;
    },
}

export default {
    state,
    mutations,
    getters
}