            <div class="links">
              <!--<div class="link" v-for="link in links" :class="link.class"
                                 @click="goToUrl(link.link_url,link.link_type)">
                                <a>{{link.link_title}}</a>
              </div>-->

              <div class="link" v-for="link in links" :key="link['link_url']" :class="link.class">
                <router-link :to="link['link_url']">{{ link["link_title"] }}</router-link>
              </div>
            </div>
          </div>
        </div>
        <!--Кнопки-->
        <div class="row" v-if="JSON.stringify(buttons) !== JSON.stringify({})">
