                class="btn btn-green"
                :key="action.action_code"
              >{{ action.name }}</button>
            </template>
          </div>
        </div>
        <!--Элементы пагинации / Зависимый dropdown-->
        <div class="row d-flex justify-content-between">
          <!--Элементы пагинации-->
          <template v-if="!sets">
            <div
              class="counter_list col hidden-md-down"
              v-if="!form_parameters.hide_pagination && !isBusy"
            >
              <p>
                {{ translateList["Showing"] }}
                <b>{{ countOf }}-{{ countTo }}</b>
                {{ translateList.of }}
                <b>{{ totalRows }}</b>
              </p>
              <v-select :options="pageOptions" :searchable="false" v-model="perPage" class="count"></v-select>
              <p>{{ translateList["onThePage"] }}</p>
            </div>
          </template>
          <template v-else-if="sets && sets['list_top']">
            <template v-for="item in sets['list_top']">
              <div
                :key="item.code"
                class="counter_list col hidden-md-down"
                v-if="item.code === 'items_per_page'"
              >
                <p>
                  {{ translateList["Showing"] }}
                  <b>{{ countOf }}-{{ countTo }}</b>
                  {{ translateList.of }}
                  <b>{{ totalRows }}</b>
                </p>
                <v-select
                  :searchable="false"
                  :components="{ OpenIndicator }"
                  :options="pageOptions"
                  v-model="perPage"
                  class="count"
                ></v-select>
                <p>{{ translateList["onThePage"] }}</p>
              </div>
            </template>
          </template>
          <!--Зависимый dropdown-->
          <div
            v-if="form_parameters.form_is_dependent"
            class="col-xl-4 col-lg-5 col-md-6 d-flex align-items-end"
          >
            <div class="list_items">
              <span
                v-if="
                  response &&
                  response.dependent &&
                  response.dependent.dependent_fields.title
                "
              >{{ response.dependent.dependent_fields.title }}</span>
              <div
                class="dropdown"
                v-if="
                  response.dependent.dependent_fields.type.includes('select')
                "
              >
                <button class="btn btn-dropdown">
                  <span class="dropdown-textover">{{ listItemsText }}</span>
                  <img class="drop-i" src="/img/dropdown-i.png" alt />
                </button>
                <!-- dropdown -->
                <transition name="fade">
                  <div class="dropdown_box">
                    <ul>
                      <!--Поиск по элементам dropdown-->
                      <li v-if="showSearch">
                        <input
                          type="text"
                          :placeholder="translateList.Search"
                          v-model="filterSelect"
                          class="dropwond_link filterSelect filter__search"
                        />
                      </li>
                      <!--Дефолтное значение-->
                      <li :class="{ selected: defaultSelectId == '' }">
                        <button
                          class="dropwond_link"
                          @click="
                            filterList('', dependent.searchFilterVal, 'select')
                          "
                        >{{ translateList.all_select }}</button>
                      </li>
                      <!--Перебор элементов-->
                      <li
                        v-for="item in filteredList"
                        :key="item['id']"
                        :class="{
                          selected:
                            defaultSelectId == item['id'] ||
                            item[dependent.searchFilterVal] == listItemsText,
                        }"
                      >
                        <button
                          @click="
                            filterList(
                              item[dependent.searchFilterVal],
                              dependent.searchFilterVal,
                              'select',
                              '',
                              item.id
                            )
                          "
                          class="dropwond_link"
                        >{{ item[dependent.searchFilterVal] }}</button>
                      </li>
                    </ul>
                  </div>
                </transition>
              </div>

              <div class="dropdown" v-if="response.dependent.dependent_fields.type == 'label'">
                <button
                  class="btn btn-dropdown"
                >{{ response.dependent.dependent_fields.current_value }}</button>
                <!-- dropdown -->
              </div>
            </div>
          </div>
        </div>
        <div class="row" v-if="block_parameters.calculate_currencies">
          <div class="col">
            <calculateCurrencies @calculateCurrenciesUp="calculateCurrencies"></calculateCurrencies>
          </div>
        </div>
        <a
          @click="filterToggle"
          v-if="$isBankNet"
          class="btn btn-green list-filter"
          :class="{ active: exFilterOn }"
        >
          <i class="fa fa-filter" aria-hidden="true"></i>
        </a>
        <!--Действия / Поиск-->
        <div
          class="row align-items-end margin-top-2"
          :class="{
            upBlock:
              !rowLine && !hideSearch && !form_parameters.hide_pagination,
            upBlock: $isBankNet,
          }"
        >
          <div class="col hidden-md-down">
            <div class="list_btn">
              <div class="list_btn_second-line" v-if="sets && sets.list_top_dropdown_actions">
                <template v-if="sets.list_top_dropdown_actions.length > 2">
                  <div class="dropdown">
                    <button class="btn btn-dropdown" :disabled="checkedItems.length === 0">
                      {{ translateList.Actions }}
                      <img class="drop-i" src="/img/dropdown-i.png" alt />
                    </button>
                    <div class="dropdown_box">
                      <ul>
                        <li v-for="item in sets.list_top_dropdown_actions" :key="item.code">
                          <button
                            :content="item.title"
                            v-tippy="{
                              placement: 'left',
                              animation: 'fade',
                              distance: '-0',
                              offset: '0,5',
                              delay: [500, 250],
                              arrow: false,
                              theme: 'info',
                            }"
                            @click="massAction(item.code, item.link)"
                            class="dropwond_link"
                          >{{ item.title }}</button>
                        </li>
                      </ul>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <a
                    class="btn"
                    :class="{
                      'btn-grey-border': !checkedItems.length,
                      'btn-light-grey': checkedItems.length,
                    }"
                    v-for="item in sets.list_top_dropdown_actions"
                    @click="massAction(item.code, item.link)"
                    :key="item.code"
                  >{{ item.title }}</a>
                </template>
                <a
                  class="btn btn-green"
                  v-for="action in actions"
                  :key="action.code"
                  v-if="action.code === 'add_row'"
                  @click="addItem(action.link)"
                >{{ action.title }}</a>
                <!--todo: это костыль, надо проработать вывод отдельных действий -->
              </div>

              <template v-if="!sets && form_parameters.hideFilter">
                <a @click="filterToggle" class="btn btn-default list-filter">
                  <img src="img/interfacerbl/rbl_filter.svg" class="svg filter-icon" alt />
                </a>
              </template>
              <template v-else>
                <template
                  v-for="item in sets.list_top_left_command_bar"
                  v-if="sets && sets.list_top_left_command_bar"
                >
                  <template v-if="item.code === 'filter'">
                    <a
                      :key="item.code"
                      @click="filterToggle"
                      class="btn btn-default list-filter"
                      :class="{ active: exFilterOn }"
                    >
                      <img :src="item.img" class="svg filter-icon" alt />
                    </a>
                  </template>
                  <template v-else>
                    <button
                      v-if="item.code === 'add'"
                      :key="item.code"
                      @click="openCard(item.code, '/new')"
                      :id="item.code"
                      :title="item.title"
                      :class="item.class"
                    >{{ item.title }}</button>
                    <button
                      v-else-if="item.code === 'receive_documents'"
                      :key="item.code"
                      @click="openCard(item.code, '/cardRequest')"
                      :id="item.code"
                      :title="item.title"
                      :class="item.class"
                    >{{ item.title }}</button>
                    <router-link
                      v-else
                      :to="item.to"
                      :id="item.code"
                      :title="item.title"
                      :key="item.code"
                      :class="item.class"
                    >{{ item.title }}</router-link>
                  </template>
                </template>
                <template
                  v-for="item in sets.list_top_left_request"
                  v-if="sets && sets.list_top_left_request"
                >
                  <template>
                    <button
                      :key="item.code"
                      v-if="item.code === 'add'"
                      @click="openCard(item.code, '/new')"
                      :id="item.code"
                      :title="item.title"
                      :class="item.class"
                    >{{ item.title }}</button>
                    <router-link
                      :key="item.code"
                      v-else
                      :to="item.to"
                      :id="item.code"
                      :title="item.title"
                      :class="item.class"
                    >{{ item.title }}</router-link>
                  </template>
                </template>
              </template>
            </div>
          </div>
          <template v-if="sets && sets.list_top" v-for="item in sets.list_top">
            <template v-if="item.code === 'items_per_page'">
              <!--Элементы пагинации для телефона-->
              <div class="col-8 hidden-md-up order-1">
                <div class="counter_list-m counter_list">
                  <p>
                    {{ translateList.Showing }}
                    <b>{{ countOfMobile }}</b>
                    {{ translateList.of }}
                    <b>{{ totalRows }}</b>
                  </p>
                  <v-select
                    :components="{ OpenIndicator }"
                    :options="pageOptions"
                    v-model="perPage"
                    :searchable="false"
                    class="count d-none d-md-block"
                  ></v-select>
                  <p>{{ translateList.onThePage }}</p>
                </div>
              </div>
            </template>
          </template>
          <!--Элементы управление слайдером на телефоне-->
          <div class="col hidden-md-up order-1">
            <div class="buttons-slider">
              <button class="btn-slider hidden-md-up slider-left" @click="changeSlide('right')">
                <img src="/img/left.png" alt />
              </button>
              <button class="btn-slider hidden-md-up slider-right" @click="changeSlide('left')">
                <img src="/img/right.png" alt />
              </button>
            </div>
          </div>
          <!--Поиск по элементам по заданому полю-->
          <template v-if="!hideSearch">
            <template v-if="!sets">
              <div
                class="col-xl-4 col-lg-5 col-md-6 col-16 d-flex align-items-end order-0"
                v-if="!form_parameters.disable_search"
              >
                <div class="search-box">
                  <input
                    type="text"
                    class="search"
                    v-model="filter"
                    @input="filterList(filter, searchNameVal)"
                    v-on:keyup.enter="filterList(filter, searchNameVal,'click')"
                    :placeholder="translateList.Search"
                  />
                  <button
                    class="search_btn"
                    :class="{'search_btn-back': paginationBackEnd}"
                    @click="filterList(filter, searchNameVal,'click')"
                  >
                    <img src="/img/interfacedashboard/Search_icon.svg" class="svg" alt />
                  </button>
                </div>
              </div>
            </template>
            <template v-else>
              <div
                class="col-xl-4 col-lg-5 col-md-6 col-16 d-flex align-items-end order-0"
                v-if="sets && sets.list_top_right_command_bar"
              >
                <template v-for="item in sets.list_top_right_command_bar">
                  <div
                    class="search-box"
                    :key="item.code"
                    v-if="
                      item.code === 'search' && !form_parameters.disable_search
                    "
                  >
                    <input
                      type="text"
                      class="search"
                      v-model="filter"
                      @input="filterList(filter, searchNameVal)"
                      v-on:keyup.enter="filterList(filter, searchNameVal,'click')"
                      :placeholder="translateList.Search"
                    />
                    <button
                      class="search_btn"
                      :class="{'search_btn-back': paginationBackEnd}"
                      @click="filterList(filter, searchNameVal,'click')"
                    >
                      <img :src="item.img" class="svg" alt />
                    </button>
                  </div>
                </template>
              </div>
            </template>
          </template>
        </div>

        <!--Легенда-->
        <div
          v-if="type === 'document' && form_parameters.show_list_legend"
          class="list-legend row margin-top-2"
        >
          <div class="col-md-2">
            <div class="list-legend-item">
              <p>{{ translateList.Payment }}:</p>
            </div>
          </div>

          <div class="list-legend-item">
            <div class="legend-color"></div>
            <p>{{ translateList.Planned }}</p>
          </div>
          <div class="list-legend-item">
            <div class="legend-color legend-grey"></div>
            <p>{{ translateList.Paid }}</p>
          </div>
          <div class="list-legend-item">
            <div class="legend-color legend-red"></div>
            <p>{{ translateList.Overdue }}</p>
          </div>
        </div>
        <div class="row">
          <div class="col" v-if="editor && block_parameters && !block_parameters.edit_mode">
            <v-select :options="['inline', 'row']" :searchable="false" v-model="editorMode"></v-select>
          </div>
          <div class="col d-flex">
            <!--	                <div class="btn btn-green" v-if="block_parameters.edit_values" @click="handlerEditor" :class="{active:editor}"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></div>-->
            <div
              class="btn btn-green btn-icon"
              @click="addLine"
              v-if="editor && editorActions.includes('addRow')"
            >
              <img class="svg" src="/img/interfacedashboard/add.svg" />
            </div>

            <div
              class="btn btn-green btn-icon"
              @click="editLine"
              v-if="
                editorRowSelect && editor && editorActions.includes('editRow')
              "
            >
              <img class="svg" src="/img/interfacedashboard/pen.svg" alt />
            </div>
            <div
              class="btn btn-green btn-icon"
              @click="removeLine"
              v-if="
                editorRowSelect && editor && editorActions.includes('removeRow')
              "
            >
              <img class="svg" src="/img/interfacedashboard/delete-2.svg" />
            </div>

            <div
              class="btn btn-green btn-icon"
              @click="dragAndDropInit('tr', 'tbody')"
              :class="{ active: dragDropEnable,'not-active': !dragDropEnable }"
              v-if="block_parameters && block_parameters.drag_and_drop"
            >
              <img class="svg" src="/img/interfacedashboard/dragdrop.svg" alt="arrow" />
            </div>

            <!-- Порядо к -->
            <div
              class="order-btns"
              v-if="
                editorRowSelect &&
                editor &&
                editorActions.includes('changeOrder')
              "
            >
              <div class="btn btn-green btn-icon" @click="changeOrder(true)">
                <img class="svg" src="/img/interfacedashboard/up.svg" />
              </div>
              <div class="btn btn-green btn-icon" @click="changeOrder(false)">
                <img class="svg" src="/img/interfacedashboard/down.svg" />
              </div>
            </div>
          </div>
        </div>
        <!--Таблица-->
        <div class="row">
          <div class="col">
            <div id="list-box">
              <div
                id="list_table-box"
                :class="{
                  disableSorting: block_parameters.edit_values,
                  exFilter: exFilterOn,
                  editTable: editor,
                  dragAndDrop: dragDropEnable,
                  preventClick: form_parameters.prevent_list_click,
                  inlineEditor: editorMode === 'inline' && editor,
                }"
              >
                <!--Таблица bootstrap-->
                <BTable
                  :sort-by.sync="sortBy"
                  :sort-desc.sync="sortDesc"
                  :empty-filtered-text="translateList.EmptyFilteredText"
                  :empty-text="translateList.EmptyFilteredText"
                  :items="items"
                  :selectable="editorRowSelect"
                  :data-path="$bRoute.path"
                  :fields="fields"
                  :hover="true"
                  :busy="isBusy"
                  :foot-clone="foot_clone && !isBusy"
                  :id="listName"
                  @row-clicked="rowClick"
                  @row-hovered="rowHover"
                  @row-selected="rowSelected"
                  @head-clicked="headClick"
                  ref="table"
                  show-empty
                  responsive
                  v-model="tableValues"
                  stacked="md"
                  thead-tr-class="resizable"
                  tbody-class="carousel"
                  thead-class="swiper-head"
                  select-mode="single"
                  selectedVariant="warning"
                >
                  <div slot="table-busy" class="text-center text-danger my-2">
                    <spring-spinner :animation-duration="3000" color="var(--accent)" />
                    <strong>Загрузка данных...</strong>
                  </div>
                  <template :slot="'HEAD_' + field.key" v-for="field in fields" slot-scope="head">
                    <div
                      class="sorting"
                      v-if="field.sortable"
                      :key="field.key"
                      :class="{
                        ascending: sortBy === field.key && sortDesc,
                        descending: sortBy === field.key && !sortDesc,
                      }"
                    ></div>
                    <div
                      :class="{
                        table__thead__overflow: !this.form_parameters
                          .list_header_break_line,
                        table_thead_break_line: this.form_parameters
                          .list_header_break_line,
                        table_thead_vertical_line: field.vertical_line,
                      }"
                      :key="field.key"
                    >{{ head.label }}</div>
                  </template>
                  <!--Слот для фильтров-->
                  <template
                    :slot="'HEAD_' + field.key"
                    v-for="field in exFilterData"
                    slot-scope="head"
                  >
                    <div
                      :content="field.label"
                      v-tippy="{
                        placement: 'top-start',
                        arrowType: 'default',
                        animation: 'fade',
                        interactive: true,
                        delay: [500, 250],
                        arrow: true,
                        theme: 'info',
                      }"
                      :key="field.key"
                    >
                      <filter-element
                        :field="field"
                        :exFilterOn="exFilterOn"
                        :form_parameters="form_parameters"
                        :type="type || ''"
                        @sortingChanged="sortingChanged"
                        @toggleSelected="toggleSelected"
                        @refreshItems="
                          (items) => {
                            refreshItems(items);
                          }
                        "
                      ></filter-element>

                      <div
                        class="sorting"
                        v-show="!exFilterOn"
                        v-if="field.sortable"
                        :class="{
                          ascending: sortBy === field.key && sortDesc,
                          descending: sortBy === field.key && !sortDesc,
                        }"
                      ></div>
                    </div>
                  </template>
                  <!--Слот для всех ячеек-->
                  <template v-for="field in fields" :slot="field.key" slot-scope="items">
                    <tableCell
                      :editorData="{
                        editor,
                        editorMode,
                        editorCell,
                        editorItem,
                      }"
                      :items="items"
                      :field="field"
                      :fields="fields"
                      :add_models="add_models"
                      :block="block"
                      :editorCellData.sync="editorCell"
                      :key="field.key"
                    ></tableCell>
                  </template>
                  <!--Слот для выбора всех элемента-->
                  <template slot="HEAD_actions">
                    <div class="list_checkbox checkbox checkbox-all">
                      <label for="checkbox-all" @click.stop>
                        <input
                          type="checkbox"
                          id="checkbox-all"
                          @click.stop="toggleSelected"
                          v-model="allSelected"
                        />
                        <span class="checkbox-custom">
                          <i class="fa fa-check"></i>
                        </span>
                      </label>
                    </div>
                  </template>
                  <!--Слот для выбора одного элемента-->
                  <template slot="actions" slot-scope="items">
                    <div class="list_checkbox checkbox checkbox-all" @click.stop>
                      <label :for="items.item.id">
                        <input
                          type="checkbox"
                          :id="items.item.id"
                          :key="items.index"
                          v-model="checkedItems"
                          :value="items.item.id"
                        />
                        <span class="checkbox-custom">
                          <i class="fa fa-check"></i>
                        </span>
                      </label>
                    </div>
                  </template>

                  <template slot="currency_input_value" slot-scope="items">
                    <div class="currency_view">
                      <div
                        class="currency_view-value"
                        @click="rowClick(items.item, items.index, $event)"
                        :content="items.item['currency_input_value']"
                        v-tippy="{
                          placement: 'left',
                          animation: 'fade',
                          distance: '-0',
                          offset: '0,5',
                          delay: [250, 250],
                          arrow: false,
                          theme: 'error',
                        }"
                      >
                        {{
                        items.item["currency_input_value"]
                        | numeralFormat(items.field.format).replace(
                        /,/g,
                        " "
                        )
                        }}
                      </div>
                      <div class="currency_view-payment">
                        {{ items.item["payment_method_name_input"] }}
                        {{ items.item["input_currency"] }}
                      </div>
                      <div class="currency_view-restriction">
                        <div>от {{ items.item["min_exchange_treshold"] }}</div>
                        <div class="max">до {{ items.item["max_exchange_treshold"] }}</div>
                      </div>
                    </div>
                  </template>

                  <template slot="currency_output_value" slot-scope="items">
                    <div
                      class="currency_view"
                      :content="items.item['currency_output_value']"
                      v-tippy="{
                        placement: 'left',
                        animation: 'fade',
                        distance: '-0',
                        offset: '0,5',
                        delay: [250, 250],
                        arrow: false,
                        theme: 'info',
                      }"
                    >
                      <div class="currency_view-value">
                        {{
                        items.item["currency_output_value"]
                        | numeralFormat(items.field.format).replace(
                        /,/g,
                        " "
                        )
                        }}
                      </div>
                      <div class="currency_view-payment">
                        {{ items.item["payment_method_name_output"] }}
                        {{ items.item["output_currency"] }}
                      </div>
                    </div>
                  </template>

                  <!--Слот для формата отчёта-->
                  <template slot="report_format" slot-scope="data">
                    <img :src="'/img/' + data.value + '.png'" alt />
                  </template>
                  <!--Слот для периода отчёта-->
                  <template slot="report_start_date" slot-scope="items">
                    <p>{{ items.item.report_start_date | moment("DD-MM-YYYY") }}</p>
                    <p>{{ items.item.report_end_date | moment("DD-MM-YYYY") }}</p>
