</template>

<script>
import $ from "jquery";
import tableCell from "./exTable/tableCell";
import "jquery-touchswipe/jquery.touchSwipe";
import { BTable } from "bootstrap-vue";
import bPagination from "bootstrap-vue/es/components/pagination/pagination";
import bFormSelect from "bootstrap-vue/es/components/form-select/form-select";
import { AtomSpinner } from "epic-spinners";
import { SpringSpinner } from "epic-spinners";
import Datepicker from "vuejs-datepicker";
import vSelect from "vue-select";
import { mapGetters } from "vuex";
import { saveFile } from "../mixins/saveFile";
import { EventBus } from "../main.js";
import numeral from "numeral";
import { dragAndDrop } from "../mixins/dragAndDrop";
import { editorTable } from "../mixins/editorTable";
import { bookmark } from "../mixins/bookmark";
import debounce from "lodash.debounce";
import calculateCurrencies from "./exTable/CalculateCurrencies";

numeral.localeData().delimiters.thousands = " ";

export default {
  mixins: [saveFile, dragAndDrop, editorTable, bookmark],
  name: "List",
  props: {
    block: Object,
    model: Array,
    type: String,
    add_models: Object
  },
  async beforeDestroy() {
    console.log("destroyList");
    this.exFilterData = [];
    this.$exFilter.state = true;
    this.saveLocalState ? await this.saveState() : false;
    clearInterval(this.requeryInterval);
    if (this.form_parameters.form_remark) this.$toast.destroy();
    window.removeEventListener("resize", this.resizeWindowListener);
  },
  data() {
    return {
      addModels: [],
      firstLoad: true,
      saveLocalState: false, //
      loadLocalState: false, //
      fullPath: this.$bRoute.fullPath,
      sets: false,
      dev: true,
      loading: true, // Флаг отображения preloader
      block_model: "",
      response: {}, // Ответ от сервера
      form_parameters: {
        form_is_dependent: false
      },
      dependent: false,
      searchNameVal: "",
      listName: "list_table",
      checkedItems: [],
      filterSelect: "",
      listItemsStash: {},
      listItemsAll: [],
      tableValues: [],
      listItemsText: "",
      defaultSelectId: "",
      sortBy: "", // Ключ колонки для сортировки
      sortDesc: true, // Направление сортировки
      section_title: "", // Название страницы
      send: "",
      archive: "",
      action: "",
      search: "",
      sizeFields: [], // Набор ширин для колонок списка
      filter: null,
      currentPage: 1, // Текущая страница пагинации
      countOfMobile: 1,
      perPage: 10, // Кол-во элементов на странице
      allSelected: false,
      pageOptions: [10, 20, 50, 100], // Варианты вывода элементов на странице
      asyncReports: "",
      fields: [], // Колонки списка
      buttons: {},
      actions: {},
      exFilterData: [],
      links: [],
      translateList: [],
      itemsDependent: [],
      exFilterOn: false, // Включение фильтра
      list_footer: null,
      foot_clone: false,
      isBusy: false,
      OpenIndicator: {
        render: createElement =>
          createElement("img", { attrs: { src: "/img/dropdown-i.png" } })
      },

      errorsInput: false,
      block_parameters: {},
      requeryInterval: null,
      calculateDirection: "from",
      calculateValue: null,
      localSorting: false,
      paginationBackEnd: false,
      totalRows: 0
    };
  },
  computed: {
    items() {
      let items = this.listItemsStash;
      if (this.model) {
        items = this.listItemsStash = this.model;
      }
      if (this.editor) {
        items = items.filter(e => e.status !== 3);
      }
      if (!this.paginationBackEnd) {
        this.totalRows = this.listItemsStash.length;
        return items.length ? items.slice(this.countOf - 1, this.countTo) : [];
      }
      return items;
    },

    showSearch() {
      let show = true;
      let windowWidth = window.innerWidth;
      let itemsLength = this.itemsDependent.length;

      if (windowWidth > 320 && itemsLength < 5) show = false;
      if (windowWidth > 768 && itemsLength < 10) show = false;
      if (windowWidth > 1200 && itemsLength < 15) show = false;

      return show;
    },
    hideSearch() {
      return (
        this.block_parameters && this.block_parameters.hide_search === true
      );
    },
    rowLine() {
      let sets =
        this.sets &&
        this.sets.hasOwnProperty("list_top_dropdown_actions") &&
        this.sets &&
        this.sets.hasOwnProperty("list_top_left_command_bar");
      return sets || Boolean(this.dependent);
    },
    countTo() {
      let countTo = this.perPage * this.currentPage;
      if (countTo > this.totalRows) {
        return this.totalRows;
      } else {
        return this.perPage * this.currentPage;
      }
    },
    countOf() {
      if (this.currentPage !== 1) {
        return this.perPage * this.currentPage - this.perPage + 1;
      }
      return 1;
    },
    filteredList() {
      // Фильтрация массива `itemsDependent`
      let res = this.itemsDependent.filter(elem => {
        if (
          elem[this.dependent.searchFilterVal] === "" ||
          this.filterSelect === ""
        )
          return true;
        else
          return (
            elem[this.dependent.searchFilterVal]
              .toLowerCase()
              .indexOf(this.filterSelect.toLowerCase()) > -1
          );
      });
      return this.removeDuplicateObject(res, this.dependent.searchFilterVal);
    },
    routeMatchedPath() {
      return this.$bRoute.matched[this.$bRoute.matched.length - 1].path;
    }
  },
  watch: {
    block: {
      handler: async function(after, before) {
        before.block_fields.forEach(field => delete field.items);
        let diffBlock = this.$isEqual(before, after);
        let diffParameters = this.$isEqual(
          before.block_parameters,
          after.block_parameters
        );
        let diffFields =
          before.block_fields.length === after.block_fields.length;

        let isDifferent = diffBlock && diffParameters && diffFields;
        if (!isDifferent) this.fillComponent(null, null, after);
      },
      deep: true
    },
    checkedItems(a, b) {
      this.allSelected =
        this.tableValues.length === a.length && a.length !== 0 ? true : false;
    },
    perPage(a, b) {
      if (a === b) return false;
      this.saveLocalState && !this.firstLoad ? this.saveState() : false;
      this.clearSelected();
      !this.firstLoad ? (this.currentPage = 1) : false;

      if (this.paginationBackEnd) this.getItemsinPage();
    },
    currentPage(a, b) {
      if (a !== b) {
        this.saveLocalState && !this.firstLoad ? this.saveState() : false;
        this.clearSelected();
        // this.items = this.listItemsStash.slice((this.countOf - 1), this.countTo);
      }
    },
    filter(a, b) {
      if (a !== b) {
        this.clearSelected();
      }
    },
    async(a, b) {
      if (a !== b) {
        this.clearSelected();
      }
    }
  },
  methods: {
    async changeUser(route, params) {
      if (!route) return false;
      try {
        let result = await this.$http.post(route, params);
        await this.$store.commit("setUserInterface", {});
        await this.$store.commit("loginUser", result.data);
        await this.showToastOk(result.data);
        this.$bRouter.push("/");
        this.$bRouter.go();
      } catch (error) {
        await this.showToastError(error.response);
        console.log(("error ChangeUser:", error));
      }
    },
    /**
     * При переходе на `Card` сохранять локальные данные в `LocalStorage`
     */
    saveLocalSettingList() {
      localStorage.setItem("perPage_" + this.routeMatchedPath, this.perPage);
      localStorage.setItem(
        "currentPage_" + this.routeMatchedPath,
        this.currentPage
      );
      localStorage.setItem(
        "windowScroll_" + this.routeMatchedPath,
        window.pageYOffset
      );
    },
    /**
     * Нажатие на Head Table
     * @param {string} key Ключ колонки
     * @param {object} obj Объект колонки
     * @param {event} event
     */
    headClick(key, obj, event) {
      if (
        event.target.classList.contains("grip") ||
        !obj.sortable ||
        this.exFilterOn ||
        this.editor ||
        this.block_parameters.edit_values
      )
        return false;
      this.localSorting = true;
      this.sortBy === key
        ? (this.sortDesc = !this.sortDesc)
        : (this.sortDesc = false);
      this.sortBy = key;

      if (this.paginationBackEnd) {
        this.getItemsinPage();
        return false;
      } else {
        this.sortingChanged({ sortBy: this.sortBy, sortDesc: this.sortDesc });
      }
    },
    /**
     * Создание новой записи
     * @param {string} code
     * @param {string} url
     */
    async openCard(code, url) {
      //owner_id:this.defaultSelectId
      if (this.form_parameters.form_is_dependent) {
        var obj = {
          controllerId: this.$bRoute.params[this.$bRoute.meta.id_field]
        };
        if (this.$bRoute.meta.prev_controller_alias) {
          obj["dependent"] = {
            controller_alias: this.$bRoute.meta.prev_controller_alias,
            id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
          };
        }
        if (
          this.dependent &&
          this.dependent.dependent_fields &&
          this.dependent.dependent_fields.type === "select"
        ) {
          let allow_creation_if_empty = this.dependent.hasOwnProperty(
            "allow_creation_if_empty"
          )
            ? this.dependent.allow_creation_if_empty
            : false;
