    async sortingChanged(ctx) {
      let self = this;
      let deep = false;
      let field = this.fields.find(e => e.key === ctx.sortBy);
      if (
        this.items.length === 0 ||
        !this.items[0].hasOwnProperty(ctx.sortBy) ||
        typeof field === "undefined"
      )
        return false;
      deep =
        this.items[0][Object.keys(this.items[0])[0]] &&
        this.items[0][Object.keys(this.items[0])[0]].hasOwnProperty("value");

      this.sortBy = ctx.sortBy;
      this.sortDesc = ctx.sortDesc;

      function sortCompare(aRow, bRow) {
        const a = deep ? aRow[ctx.sortBy].value : aRow[ctx.sortBy];
        const b = deep ? bRow[ctx.sortBy].value : bRow[ctx.sortBy];
        const type = field.hasOwnProperty("typeVal") ? field.typeVal : "string";

        if (toString(a).length === 0) return -1;
        if (type === "number") {
          return Number(a.toString().replace(/\s/g, "")) <
            Number(b.toString().replace(/\s/g, ""))
            ? -1
            : Number(a.toString().replace(/\s/g, "")) >
              Number(b.toString().replace(/\s/g, ""))
            ? 1
            : 0;
        } else if (type === "date") {
          return self
            .$moment(a, "YYYY-MM-DDTHH:mm:ss.SSS")
            .isAfter(self.$moment(b, "YYYY-MM-DDTHH:mm:ss.SSS"))
            ? 1
            : -1;
        } else {
          return toString(a).localeCompare(toString(b));
        }
      }

      if (!ctx.sortDesc) {
        this.listItemsStash.sort(sortCompare);
      } else {
        this.listItemsStash.sort(sortCompare).reverse();
      }
      !this.firstLoad && !this.paginationBackEnd
        ? (this.currentPage = 1)
        : false;

      function toString(value) {
        if (value === null || typeof value === "undefined") {
          return "";
        } else if (value instanceof Object) {
          return Object.keys(value)
            .sort()
            .map(key => toString(value[self.sortBy]))
            .join(" ");
        } else {
          return String(value);
        }
      }
    },
    /**
     * Фильтрация таблицы
     * @param {string} searchVal               Текст поиска
     * @param {string} tableColumn             Колонка таблицы
     * @param {string} type                    Тип поиска
     * @param {string} start
     * @param {string} itemId
     * @return {array} this.listItemsStash
     */
    filterList: debounce(function(searchVal, tableColumn, type, start, itemId) {
      let self = this;
      if (this.paginationBackEnd && type === "click") {
        this.getItemsinPage();
        return false;
      }
      if (this.paginationBackEnd) {
        return false;
      }
      // Проверка типа фильтра
      if (type === "select" && !start) {
        if (searchVal !== "") {
          this.listItemsText = searchVal;
          this.defaultSelectId = itemId;
        } else {
          this.listItemsText = this.translateList.all_select;
          this.defaultSelectId = "";
        }
      }
      // Фильтрация
      this.listItemsStash = this.listItemsAll.filter(elem => {
        let columnVal =
          tableColumn && tableColumn.length > 0
            ? self.$getProp(elem, tableColumn)
            : null;
        if (searchVal === "") return true;
        if (columnVal === undefined) return false;
        if (!searchVal) return false;
        if (type === "select")
          return (
            "" + columnVal.toLowerCase() ===
            (searchVal.split(" - ")[1] || searchVal).toLowerCase()
          );
        if (!tableColumn)
          return JSON.stringify(elem)
            .toLowerCase()
            .includes(searchVal.toLowerCase());
        return ("" + columnVal).toLowerCase().includes(searchVal.toLowerCase());
      });
    }, 500),
