    /**
     * Получение ширин колонок из localStorage
     * @param {string} name
     * @return {array} Массив ширин
     */
    getWidthFromLocal(name) {
      // Проверка `localStorage` на наличие
      if (localStorage.getItem(name))
        return localStorage.getItem(name).split(",");
      // Вернуть строе значение
      return this.sizeFields;
    },
    /**
     * Добавление возможности изменять ширину колонок,
     * Сохранение ширин в localStorage
     * @param {string} name
     * @param {object} sizes
     * @param {boolean} localWidth
     * @param {boolean} removeActions
     */
    addResizeTable(name, sizes, localWidth, removeActions) {
      let self = this; // Vue this
      let thElem; // Активный `th`
      let thSumWidth; // сумма ширин для `th` активного + следующий после активного
      let startOffset; // Начальное значение курсора мыши при клике на элемент `grip`
      let arrayTh = document.querySelectorAll(
        "#" + this.listName + " thead th"
      ); // Массив `thead th`
      if (document.querySelector("#" + this.listName)) {
        var widthTable = document
          .querySelector("#" + this.listName)
          .closest("#list-box").offsetWidth; // Ширина таблицы
      } else {
        return false;
      }
      // Цикл колонок thead
      arrayTh.forEach((th, index) => {
        if (arrayTh.length > index + 1 || removeActions) {
          th.style.width =
            sizes[index] && localWidth // Перевод % в px
              ? (widthTable * parseInt(sizes[index])) / 100 + "px"
              : (widthTable * parseInt(this.fields[index].thStyle)) / 100 +
                "px";

          if (th.querySelector(".grip")) return false;
          // Создание и заполнение дополнительного элемента
          var grip = document.createElement("div");
          grip.classList.add("grip");
          grip.innerHTML = "&nbsp;";
          th.appendChild(grip);
          // end
          // Добавление события `mousedown` на созданый дополнительный элемент
          grip.addEventListener("mousedown", function(e) {
            thElem = th;
            startOffset = th.offsetWidth - e.pageX;
            thSumWidth = th.offsetWidth + th.nextElementSibling.offsetWidth;
            e.preventDefault();
            e.stopPropagation();
          });
        }
      });

      // Добавление события `mousemove` на document
      document.addEventListener("mousemove", function(e) {
        // Проверка на существование активного th`
        // Проверка на минимальную ширину колонки
        if (
          !thElem ||
          thSumWidth - (startOffset + e.pageX) < 90 ||
          thSumWidth - (startOffset + e.pageX) > thSumWidth - 90
        )
          return false;
        thElem.style.width = startOffset + e.pageX + "px"; // Новое значение ширины для колонки
        thElem.nextElementSibling.style.width =
          thSumWidth - parseInt(thElem.style.width) + "px"; // Новое значение ширины для следующей колонки
      });
      // Добавление события `mouseup` на document
      document.addEventListener("mouseup", addResize);

      function addResize() {
        // Массив ширин колонок в `%`
        let arrWidthTh = [];
        arrayTh.forEach((th, i) => {
          // Заполнение массива
          arrWidthTh.push(Math.round(100 / (widthTable / th.offsetWidth)));
        });
        // При изменении ширины колонок идёт запись в localStorage
        if (arrWidthTh[0] != 0 && self.$bRoute.meta.save_local_storage_l)
          localStorage.setItem(self.routeMatchedPath, arrWidthTh);
        thElem = undefined; // Удаление активного `th`
      }

      addResize();

      this.refreshTable(); // Обновление таблицы
    },
    mobileTable() {
      let self, list, table;
      self = this;
      list = $("#" + this.listName + " .carousel").addClass("list_mobile");
      table = $("#list-box");
      self.perPage = 1;
      this.$store.state.menu_open = false;
      let timerReadyTable = setInterval(function() {
        let lengthTr = $(list).find("tr").length;
        if (lengthTr === self.totalRows || lengthTr === self.perPage) {
          clearInterval(timerReadyTable);
          $(list).attr("data-item", "1");
          if (
            $(list)
              .children("tr")
              .children(".list_actions-box").length > 0
          )
            $(table).addClass("list_actions_enable");
          self.countOfMobile = 1;
          $("#" + this.listName).swipe({
            allowPageScroll: "auto",
            swipe: function(
              event,
              direction,
              distance,
              duration,
              fingerCount,
              fingerData
            ) {
              self.changeSlide(direction);
            }
          });
        }
        self.equalMobileColumn();
      }, 500);
    },
    destroyMobileTable() {
      $(".list_mobile")
        .css("transform", "initial")
        .removeClass("list_mobile");
      this.perPage = 20;
      if (this.block_parameters.hide_pagination) this.perPage = 1000;
      // this.items = this.listItemsStash.slice(0, this.perPage);
    },
    resizeWindowListener() {
      let self = this;
      // self.readyTable(self.form_parameters.form_code);
      if (
        window.innerWidth < 768 &&
        !$("#" + this.listName + " tbody").hasClass("list_mobile")
      )
        self.mobileTable();
      else if (
        window.innerWidth > 768 &&
        $("#" + this.listName + " tbody").hasClass("list_mobile")
      )
        self.destroyMobileTable();
    },
    changeSlide(direction) {
      let currentPage, list;
      list = $(".list_mobile");
      if (direction === "left" && this.currentPage < this.totalRows) {
        currentPage = parseInt($(list).attr("data-item"));
        $(list).attr("data-item", ++currentPage);
        this.currentPage++;
        this.equalMobileColumn();
      } else if (direction === "right" && 1 < this.currentPage) {
        currentPage = parseInt($(list).attr("data-item"));
        $(list).attr("data-item", --currentPage);
        this.currentPage--;
        this.equalMobileColumn();
      }
    },
    refreshExFilter() {
      let { items, data } = this.$exFilter.refreshRules(this.listItemsStash);
      this.listItemsStash = items;
      this.exFilterData = data;
    },
    /**
     * Готовность таблицы
     * @param {string} page
     */
    async readyTable(page) {
      let removeActions = false;
      let localWidth = false;
      // Отключение preloader
      this.loading = false;
      if (this.$exFilter.state !== true || this.firstLoad) {
        this.exFilterData = this.$exFilter.init(
          this.fields,
          this.listItemsStash,
          this.translateList
        );
      } else {
        await this.refreshExFilter();
      }

      await this.checkRouteSaved();

      let calculateData = this.$store.getters.getCalculateData;

      if (this.$isBankNet && calculateData.value && calculateData.direction) {
        this.calculateCurrencies({
          direction: calculateData.direction,
          value: calculateData.value
        });
      }

      if (
        (this.sets &&
          this.sets.list_top_dropdown_actions &&
          this.sets.list_top_dropdown_actions.lenth > 0 &&
          this.fields[0].key === "actions") ||
        (this.sets &&
          !this.sets.list_top_dropdown_actions &&
          this.fields[0].key === "actions") ||
        (!this.sets && this.fields[0].key === "actions")
      ) {
        removeActions = true;
        this.fields.shift();
      }
      this.$nextTick(async () => {
        if (this.form_parameters.form_remark)
          this.$toast.warning(this.form_parameters.form_remark, "", {
            timeout: 999999999,
            progressBar: false,
            position: "topRight",
            displayMode: 2
          });
        // Преобрахование тега `img.svg` в SVG
        this.imgtosvg();
        // Получение ширины из LocalStorage
        this.sizeFields = this.getWidthFromLocal(
          this.$bRoute.matched[this.$bRoute.matched.length - 1].path
        );

        if (this.paginationBackEnd) {
          // Начальная сортировка
          await this.sortingChanged({
            sortBy: this.sortBy,
            sortDesc: this.sortDesc
          });
        }

        this.sizeFields.length === this.fields.length
          ? (localWidth = true)
          : false;

        this.loadLocalState ? await this.loadState() : false;

        // Проверка на мобильную версию
        if (window.innerWidth < 768) {
          // Построение мобильной таблицы
          this.mobileTable();
          this.equalMobileColumn();
        } else {
          // Добавление ранее полученой ширины в таблицу
          this.addResizeTable(
            page,
            this.sizeFields,
            localWidth && this.loadLocalState,
            removeActions
          );
        }
        // Проверка на локальный perPage
        // let perPageLocal = localStorage.getItem('perPage_' + this.routeMatchedPath);
        // this.perPage = perPageLocal ? parseInt(perPageLocal) : this.perPage;
        this.$nextTick(async () => {
          // Проверка на локальный currentPage
          // let currentPageLocal = localStorage.getItem('currentPage_' + this.routeMatchedPath);
          // this.currentPage = currentPageLocal ? parseInt(currentPageLocal) : 1;
          // Обновление элементов в таблице
          // this.items = this.listItemsStash.slice(this.countOf - 1, this.countTo);
          // Редактирование списка
          this.block_parameters.edit_values ? this.handlerEditor() : false;
          // Обновление таблицы
          this.refreshTable();
          setTimeout(() => {
            this.firstLoad = false;
            EventBus.$emit("initializationEvent", { event: "click" });
          }, 1000);
        });
      });
    },
    /**
     * На мобильной версии табилцы выровнять `th` и `td`
     */
    equalMobileColumn() {
      try {
        let table = document.querySelector("#" + this.listName),
          thArray = table.querySelectorAll("thead th"),
          tdArray = table.querySelectorAll("tbody td");
        if (thArray.length !== tdArray.length)
          throw new Error(" Длина не совпадает `equalMobileColumn()`");
        thArray.forEach((th, index) => {
          // Проверка на колонку `list_checkbox` (checkbox)
          // В мобильной версии её не показываем
          if (
            th.classList.contains("list_checkbox") &&
            tdArray[index].classList.contains("list_checkbox")
          )
            return false;
          var thHeight = th.offsetHeight;
          var tdHeight = tdArray[index].offsetHeight;
          if (thHeight === tdHeight) return false;
          if (thHeight > tdHeight)
            tdArray[index].style.height = thHeight + "px";
          else th.style.height = tdHeight + "px";
        });
      } catch (e) {
        console.error("Ошибка " + e.name + ":" + e.message + "\n" + e.stack);
      }
    },
    /**
     * Обновление таблицы
     */
    refreshTable() {
      this.$nextTick(() => {
        this.imgtosvg();
        if (this.model) {
          this.listItemsAll = this.model;
        }
        this.$refs.table
          ? this.$refs.table.refresh()
          : console.log("table doesn't exist");
      });
    },
    /**
     * Удаление дубликатов из массива
     * @param {array} arr
     * @return {array} arr
     */
    removeDuplicate(arr) {
      return arr.filter((item, pos, self) => self.indexOf(item) === pos);
    },
    removeDuplicateObject(array, field) {
      if (array[0] === undefined) return false;
      return array.filter((obj, pos, arr) => {
        return arr.map(mapObj => mapObj[field]).indexOf(obj[field]) === pos;
      });
    },
    refreshItems(items) {
      this.listItemsStash = items;
      // this.items = this.listItemsStash.length ? this.listItemsStash.slice((this.countOf - 1), this.countTo) : [];
      this.refreshTable();
    },
    /**
     * Получение универсального JSON
     * @param {string} Request
     * @param {object} params
     */
    requestData(Request, params = null) {
      this.$http
        .post(Request, params)
        .then(res => {
          let block = res.data.tabs[0].blocks[0];
          let model = res.data.main_data_models[block.block_model];
          this.fillComponent(
            model,
            res.data.add_data_models,
            block,
            res.data.form_parameters,
            res.data.links,
            res.data.sets,
            res.data.dependent,
            res.data.actions,
            res.data
          );

          this.showToastOk(res.data);
        })
        .catch(error => {
          this.showToastError(error);
          this.$bRouter.push({ path: "/" + error.response.status });
        });
    },
    addItem(link) {
      // this.$store.commit('changeAddingLink', link);
      // this.$store.commit('changeAddingDependencyField', this.$bRoute.params.id);
      this.$bRouter.push(this.$bRoute.path + "/new");
    },
    filterToggle() {
      this.exFilterOn = !this.exFilterOn;
    },

    photoLoad: function(item, key, e, model) {
      var reader = new FileReader();
      if (e.target.files[0]) {
        reader.readAsDataURL(e.target.files[0]);

        this.$validator.validate(item[key].input_name).then(result => {
          if (result) {
            reader.onload = e => {
              item[key].value = e.target.result;
              this.refreshFilter(model);
            };

            item[key].name = e.target.files[0].name; // set the name of loaded file in the button
            item.doc_size.value =
              Math.round(e.target.files[0].size / 1024, 1) + "KB"; //set the size of file in the mode

            item.doc_ext.value = e.target.files[0].name.substring(
              e.target.files[0].name.lastIndexOf(".") + 1
            );
          } else {
            item[key].value = null; // clean the model
            item[key].name = null; // clean the model
            item.doc_size.value = null; //clean the model
            item.doc_ext.value = null; // clean the model
            this.errors.items.forEach(err => {
              if (err.field === item[key].input_name) {
                this.$toast.error(err.msg, "", { position: "topRight" });
              }
            });
            if (e.target.classList.contains("notVerify")) {
              this.items.pop();
              this.listItemsAll.pop();
              return false;
            }
          }
        });
      } else {
        //                    reader.readAsDataURL(e.target.files[0]);
        //                    this.refreshFilter(model);
      }
    },
    addRow(block) {
      let model = block.block_model;

      var obj = JSON.parse(JSON.stringify(this.items[0]));

      for (var item in obj) {
        if (item == "status") obj[item] = "new";
        // меняем флаг, чтобы видеть, что строка новая
        else if (item == "id") {
          obj[item] = model + "_new_id_" + block.current_new_id; // меняем флаг, чтобы видеть, что input  новая todo проверить надо ли
          block.current_new_id++; // увеличиваем счетчик текущего нового айдишника
        } else if (item == "_rowVariant") {
          obj[item] = "";
        } else {
          obj[item].value = null;
          if (item == "document") {
            obj[item].input_name = model + "_new_" + block.current_new_id;
            obj[item].name = null;
          }
        }
      }

      this.listItemsAll.push(obj);
      this.refreshFilter();
      this.$nextTick(() => {
        let self = this;
        let inputFile = document.querySelectorAll(
          'tbody tr:last-child input[type="file"]'
        )[0];
        inputFile.classList.add("notVerify");
        inputFile.onclick = charge;
        inputFile.click();

        function charge() {
          document.body.onfocus = checkFile;
        }

        function checkFile() {
          let inputFile = document.querySelectorAll(
            'tbody tr:last-child input[type="file"]'
          )[0];
          setTimeout(function() {
            if (!inputFile.value.length) {
              self.items.pop();
              self.listItemsAll.pop();
            }
          }, 500);
          document.body.onfocus = null;
        }
      });
      // this.listItemsStash[model].push(obj);
    },
    photoPreview(src, ext) {
      if (src == "") return;
      if (ext != "pdf")
        this.$store.commit("showModal", {
          type: "htmlModal",
          html: "<img src=" + src + ">"
        });
      else
        this.$store.commit("showModal", {
          type: "htmlModal",
          html: "<embed src=" + src + ">"
        });
    },
    removeDocument(item, model) {
      item.status = "deleted";
      this.refreshFilter();
    },
    downloadFile(href, params) {
      if (href != "")
        this.$http.post(href, params).then(res => {
          // console.log(res.data);
          this.saveFile(res.data);
        });
      else return;
    },
    deepValue(value) {
      return value.hasOwnProperty("value") ? value.value : value;
    },

    async saveState() {
      await this.$store.dispatch("listState", {
        perPage: this.perPage,
        currentPage: this.currentPage,
        scroll: window.pageYOffset,
        page: this.fullPath
      });
      console.log("saveState", {
        perPage: this.perPage,
        currentPage: this.currentPage,
        scroll: window.pageYOffset,
        page: this.fullPath
      });
    },
    async loadState() {
      let state = this.$store.getters.listsList[this.fullPath];
      console.log("loadState", state);
      if (this.firstLoad) {
        this.perPage = state.perPage;
        await setTimeout(async () => {
          this.currentPage = state.currentPage;
          window.scrollTo(0, state.scroll);
        }, 500);
      }
    },
    checkRouteSaved() {
      let localL = this.$bRoute.meta.save_local_storage_l || false;
      if (!localL) {
        let lists = Object.keys(this.$store.getters.listsList);
        lists.includes(this.fullPath)
          ? this.$store.commit("removeListPage", this.fullPath)
          : false;
      }
      let returnPage = this.$store.getters.lastPage.old === this.fullPath;
      this.saveLocalState = localL;
      this.loadLocalState = this.saveLocalState && returnPage;
      console.log("load", this.loadLocalState);
    },

    fillComponent(
      model,
      addModels,
      block,
      form_parameters,
      links,
      sets,
      dependent,
      actions,
      response
    ) {
      // Название модели
      this.block_model = block.block_model;

      // Получение всех дополнительных моделей
      if (addModels) {
        this.addModels = addModels;
      }

      // Полный ответ от серевера
      if (response) {
        this.response = response;
      }

      function setFormParameters() {
        this.form_parameters = form_parameters;
        // Зависимость формы к select
        this.form_parameters.form_is_dependent =
          block.form_is_dependent || form_parameters.form_is_dependent;

        // По какому полю поиск
        this.searchNameVal = form_parameters.form_type_list.form_search_field;

        // Сортировка
        // Начальная сортировка `key`
        if (!this.localSorting) {
          this.sortBy = form_parameters.form_sort_by || "created_at";
          // Направление сортировки
          this.sortDesc = form_parameters.form_sort_desc || true;
        }

        // Зависимость
        if (form_parameters.form_is_dependent && dependent) {
          this.dependent = dependent;
          if (dependent.dependent_fields.type.includes("select")) {
            // Начальное значение select
            this.defaultSelectId =
              dependent.dependent_fields.options_data.options_field_id_value;
            // По какому полю фильтровать
            this.dependent.searchFilterVal =
              dependent.dependent_fields.options_data.options_field_title;
            // Получение всех элементов
            if (addModels) {
              this.itemsDependent =
                addModels[
                  dependent.dependent_fields.options_data.options_data_model
                ];
            } else {
              this.itemsDependent = model;
            }
            if (this.defaultSelectId === "") {
              this.listItemsText = this.translateList.all_select;
            } else {
              // Поиск начального значение поля
              this.itemsDependent.forEach(elem => {
                if (elem["id"] == this.defaultSelectId) {
                  this.listItemsText = elem[this.dependent.searchFilterVal];
                  this.filterList(
                    elem[this.dependent.searchFilterVal],
                    this.dependent.searchFilterVal,
                    "select",
                    1
                  );
                }
              });
            }
          }
        }
      }

      function setBlock() {
        // Параметры блока
        if (block.block_parameters) {
          this.block_parameters = { ...block.block_parameters };

          // Перезапрос на обновление данных
          clearInterval(this.requeryInterval);
          if (this.block_parameters.requery_interval) {
            // Получение пары валют для bankNet
            let match = this.$store.getters.getMatchCurrencies;
            let data;
            if (this.$isBankNet && match.left && match.right) {
              data = {
                exchange_direction_id_input: match.left,
                exchange_direction_id_output: match.right
              };
            }
            this.requeryInterval = setInterval(() => {
              this.requestData(this.$bRoute.meta.route, data);
            }, this.block_parameters.requery_interval);
          }

          // Обновление данных
          this.isBusy = block.block_parameters.busy || false;

          // Уникальное имя списка
          if (this.block_parameters.unique_name)
            this.listName =
              this.listName + "_" + this.block_parameters.unique_name;

          // Спрятать пагинацию
          if (block.block_parameters.hide_pagination) this.perPage = 1000;

          // дописываем флаг на блокировку клика на списке
          this.form_parameters.prevent_list_click =
            block.block_parameters.prevent_list_click;
          // дописываем флаг на блокировку легенды на списке
          this.form_parameters.show_list_legend =
            block.block_parameters.show_list_legend;

          this.form_parameters.hide_filter = block.block_parameters.hide_filter;

          this.form_parameters.list_header_break_line =
            block.block_parameters.list_header_break_line;

          //добавление флага на скрытие пагинации в табличной части
          this.form_parameters.hide_pagination =
            block.block_parameters.hide_pagination;

          this.paginationBackEnd =
            block.block_parameters.enable_pagination_request;

          if (this.paginationBackEnd) {
            this.totalRows = block.block_parameters.total_items;
          }
        }

        // если есть подвал списка - cохраняем его в компоненте
        if (block.list_footer) {
          this.list_footer = block.list_footer;
          this.foot_clone = true;
        }

        // Кнопки
        if (block.buttons) {
          this.buttons = this.block.block_actions;
        }

        // Колонки в списке
        this.fields = [...block.block_fields];
      }

      // Параменты формы
      if (form_parameters) {
        setFormParameters.apply(this);
      }

      if (block) {
        setBlock.apply(this);
      }

      // Сэты
      if (sets) {
        this.sets = sets || false;
      }
      if (this.$isBankNet) {
        this.sets = {
          list_top_right_command_bar: [{ code: "search", title: "Поиск" }]
        };
      }
      // actions
      if (actions) {
        this.actions = actions.actions_list || false;
      }

      // Ссылки
      if (links) {
        this.links = links;
      }

      // Проверка на мобильную версию
      if (window.innerWidth < 768) this.perPage = 1;

      if (model) {
        // Все элементы
        this.listItemsAll = model;
        // Элементы для фильтрации
        this.listItemsStash = model;
      }
      // Проверка на готовность таблицы
      this.readyTable(this.block_model);
    },
    async showToastOk({ template, title, message, position }) {
      await this.$store.dispatch("showToast", {
        template: template,
        title: title,
        message: message,
        position: position
      });
    },
    async showToastError(error) {
      await this.$store.dispatch("showToast", {
        template: "error",
        title: error.status,
        message: error.data.message,
        position: error.data.position
      });
    },
    // Калькулятор валют
    async calculateCurrencies({ direction, value }) {
      let from = "currency_input_value",
        to = "currency_output_value",
        rate = "currency_rate";

      this.listItemsStash.forEach(item => {
        let minTres = parseFloat(item["min_exchange_treshold"]);
        let maxTres = parseFloat(item["max_exchange_treshold"]);
        if (direction === "from") {
          item[from] = parseFloat(value);
          item[to] = parseFloat(value) * parseFloat(item[rate]);
          if (minTres <= parseFloat(value) && parseFloat(value) <= maxTres) {
            item["_rowVariant"] = null;
          } else {
            item["_rowVariant"] = "disable";
          }
        } else {
          item[to] = parseFloat(value);
          item[from] = parseFloat(value) / parseFloat(item[rate]);
          if (item[from] >= minTres && item[from] <= maxTres) {
            item["_rowVariant"] = null;
          } else {
            item["_rowVariant"] = "disable";
          }
        }
      });
      if (direction === "from") {
        this.sortBy = to;
        this.sortDesc = true;
      } else {
        this.sortBy = from;
        this.sortDesc = false;
      }
      if (this.localSorting === false) {
        this.localSorting = false;
        this.sortingChanged({ sortBy: this.sortBy, sortDesc: this.sortDesc });
      }
      await this.refreshExFilter();
    }
  },
  async created() {
    let res = "";
    res = await this.$http.post("/menu/translateList/public", null);
    try {
      this.translateList = res.data;
      if (this.block && this.model) {
        this.fillComponent(this.model, this.addModels, this.block);
      } else {
        var data = {
          id: this.$bRoute.params[this.$bRoute.meta.id_field]
        };
        if (this.$bRoute.meta.prev_controller_alias) {
          data["dependent"] = {
            controller_alias: this.$bRoute.meta.prev_controller_alias,
            id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
          };
        }
        // Получение пары валют для bankNet
        let match = this.$store.getters.getMatchCurrencies;
        if (this.$isBankNet && match.left && match.right) {
          var data = {
            exchange_direction_id_input: match.left,
            exchange_direction_id_output: match.right
          };
        }

        this.requestData(this.$bRoute.meta.route, data);
      }
      await this.showToastOk(res.data);

      this.checkIfBookmarked();
    } catch (error) {
      await this.showToastError(error.response);
    }
  },
  mounted() {
    if (this.$bRoute.path.includes("Dev")) this.dev = false;
    global.list = this;
    console.log("created: ", this.exFilterData);

    window.addEventListener("resize", this.resizeWindowListener);
    this.$store.commit("changeTab", 0);
  },
  components: {
    BTable,
    bPagination,
    bFormSelect,
    AtomSpinner,
    SpringSpinner,
    Datepicker,
    vSelect,
    tableCell,
    calculateCurrencies
  }
};
</script>

