router.beforeEach((to, from, next) => {
  if (to.path === "/" && store.getters.isLoggedIn) {
    store.getters.home_url && store.getters.home_url.length > 2
      ? router.replace(store.getters.home_url)
      : next();
    return false;
  }
  store.commit("deleteNotification", to.path);
  store.dispatch("lastPage", from.path);
  next();
});

function updateAxios(token = null) {
  let tokenNew = token !== null ? token : store.getters.token;
  const base = axios.create({
    baseURL: process.env.VUE_APP_BASE_URL,
    headers: {
      Authorization: "Bearer " + tokenNew,
      lang: localStorage.getItem("lang") || "ru",
    },
    withCredentials: true,
  });
  base.interceptors.response.use(
    (response) => {
      if (response.headers["authorization"]) {
        store.commit(
          "loginUser",
          response.headers["authorization"].split(" ")[1]
        );
      }
      return response;
    },
    function (error) {
      let originalRequest = error.config;
      if (
        error.response.status === 500 &&
        process.env.VUE_APP_BASE_DEBUG.includes("true")
      ) {
        let contentBox = document.querySelector(".main__content");
        if (!contentBox) return false;
        let text = "<pre>message: " + error.response.data.message + "</pre>";
        text += "<pre>file: " + error.response.data.file + "</pre>";
        text += "<pre>line: " + error.response.data.line + "</pre>";
        text +=
          '<pre style="white-space: pre-line;">' +
          JSON.stringify(error.response.data.trace) +
          "</pre>";
        let html = '<div class="container">' + text + "</div>";
        contentBox.innerHTML = html;
        return false;
      }
      if (error.response.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true;

        store.commit("logoutUser");
        // window.location = '/login';
      }
      return Promise.reject(error);
    }
  );

  // Общие функции и прототипы
  Vue.prototype.$http = base;
}
updateAxios();

global.Vue = Vue;
Array.prototype.clean = function (deleteValue) {
  for (var i = 0; i < this.length; i++) {
    if (this[i] == deleteValue) {
      this.splice(i, 1);
      i--;
    }
  }
  return this;
};

function stringToPath(string) {
  let rePropName = /[^.[\]]+|\[(?:(-?\d+(?:\.\d+)?)|(["'])((?:(?!\2)[^\\]|\\.)*?)\2)\]|(?=(?:\.|\[\])(?:\.|\[\]|$))/g;
  let reEscapeChar = /\\(\\)?/g;
  var result = [];
  string.replace(rePropName, function (match, number, quote, string) {
    result.push(quote ? string.replace(reEscapeChar, "$1") : number || match);
  });
  return result;
}
Vue.prototype.$log = console.log;
Vue.prototype.$isEqual = isEqual;
Vue.prototype.$getProp = function (obj, keys, defaultValue) {
  keys = Array.isArray(keys) ? keys : stringToPath(keys);
  // keys = keys.filter(e=>!Number.isInteger(+e));
  obj = obj[keys[0]];
  if (obj && keys.length > 1) {
    return Vue.prototype.$getProp(obj, keys.slice(1));
  }
  return obj === undefined ? defaultValue : obj;
};
