const state = {
    table_fields: [],
    fields_parameters: [],
    depths_parameters: []
};


const mutations = {
    setFormParameters(state, data) {
        state.table_fields = data.table_fields;
        state.fields_parameters = data.fields_parameters;
        state.depths_parameters = data.depths_parameters;
    }
};

const getters = {
    /**
     *
     * @param state
     * @returns {function(*): *}
     *
     * возвращает настройки уровня определенной вложенности
     */
    getDepthParameters: (state) => (depth) => {
        return state.depths_parameters[depth - 1];
    },
    /*
    возвращает список колонок
     */
    getTableFields: (state) => {
        return state.table_fields;
    },
    getFieldsParameters: (state) => {
        return state.fields_parameters;
    }
};

export default {
    state,
    mutations,
    getters
}
