<template>
    <b-modal
            v-model="modalShow"
            ref="b-modal"
            @ok="handleOk"
            @hidden="handleCancel"

    >
        <template v-slot:modal-header>

            <h4>{{modal.modal_title}}</h4>
        </template>

        <template v-slot:default>
            <template
                    v-if="modal.type === 'editRow' || modal.type==='editFromCard' || modal.type === 'addRowWithRequest'">
                <div class="field-box" v-for="field in editFields">
                    <label v-if="field.type!=='checkbox'">{{field.label}}</label>
                    <v-select
                            v-if="field.type === 'select' "
                            v-validate="field.validation"
                            :ref="field.key"
                            :name="field.key"
                            :class="{'valid':fieldsVV[field.key]&&fieldsVV[field.key].valid&&fieldsVV[field.key].touched && field.validation, 'invalid':fieldsVV[field.key]&&fieldsVV[field.key].invalid&&fieldsVV[field.key].touched && field.validation}"
                            item-text="name"
                            item-value="last"
                            :label="field.options_data.options_field_title"
                            :options="getOptionSelect(model,field)"
                            @input="chooseOption(field,model)"
                            :value="model[field.key]"
                            :reduce="obj => obj[field.options_data.options_field_id]"
                            v-model="model[field.key]">
                        <div slot="no-options">Ничего не найденно!</div>
                    </v-select>
                    <template v-else-if="field.type === 'date'">
                        <datepicker
                                v-validate="field.validation"
                                :name="field.key"
                                :ref="field.type"
                                :class="{'valid':fieldsVV[field.key]&&fieldsVV[field.key].valid&&fieldsVV[field.key].touched && field.validation, 'invalid':fieldsVV[field.key]&&fieldsVV[field.key].invalid&&fieldsVV[field.key].touched && field.validation}"
                                :format="customFormatter"
                                v-model="model[field.key]"
                        ></datepicker>
                    </template>
                    <template v-else-if="field.type==='checkbox'"
                    >
                        <div class="contractor_checkbox checkbox "
                             :class="{'checkbox-disabled':field.disabled}"
                        >
                            <label :for="field.model">
                                <input :disabled="field.disabled" :name="field.model"
                                       type="checkbox" :id="field.model"
                                       v-model="model[field.key]"
                                       class="basic-input"
                                       v-validate="field.validation"
                                >
                                <span class="checkbox-custom"><i class="fa fa-check"></i></span>
                                <span> {{field.label}}</span>
                            </label>
                        </div>
                    </template>
                    <template v-else>
                        <p v-if="field.typeVal === 'number' && field.format">
                            <input
                                    v-validate="field.validation"
                                    type="text"
                                    :ref="field.type"
                                    :disabled="field.disabled"
                                    :name="field.key"
                                    :class="{'valid':fieldsVV[field.key]&&fieldsVV[field.key].valid&&fieldsVV[field.key].touched && field.validation, 'invalid':fieldsVV[field.key]&&fieldsVV[field.key].invalid&&fieldsVV[field.key].touched && field.validation}"
                                    :value="model[field.key] | numeralFormat(field.format).replace(/,/g,' ')">
                        </p>
                        <input
                                v-else
                                v-validate="field.validation"
                                type="text"
                                :ref="field.type"
                                :disabled="field.disabled"
                                :name="field.key"
                                :class="{'valid':fieldsVV[field.key]&&fieldsVV[field.key].valid&&fieldsVV[field.key].touched && field.validation, 'invalid':fieldsVV[field.key]&&fieldsVV[field.key].invalid&&fieldsVV[field.key].touched && field.validation}"
                                v-model="model[field.key]">
                    </template>

                    <span class="vv-error">
                {{ errors.first(field.key)}}</span>
                </div>
            </template>

            <template v-else-if="modal.type==='previewFile'">

                <embed :src="model[model_name][0]['file']"
                       v-if="model[model_name][0]['extension']==='pdf'"
                       type="application/pdf">

                <div class="img-container" v-else>
                    <img :src="model[model_name][0]['file']" alt="">
                </div>

            </template>

            <template v-for="field in modal.fields" v-else>
                <div class="field-box">
                    <label>{{field.title}}</label>
                    <input type="text"
                           v-model="model[field.model]"
                           :name="field.model"
                           :type=field.type
                           :ref="field.ref"
                           v-validate="field.validation"

                    >
                    <span class="vv-error">
                {{ errors.first(field.model)}}</span>
                </div>
            </template>

        </template>
        <template v-slot:modal-footer="{ ok, cancel }">

            <button class="btn btn-grey" size="sm" @click="cancel()">
                Отмена
            </button>

            <button class="btn btn-green" size="sm" @click="ok()">
                OK
            </button>

        </template>
    </b-modal>
</template>

<script>
    import {BModal} from 'bootstrap-vue';
    import Zone from './Zone';
    import {EventBus} from "../main";
    import Datepicker from 'vuejs-datepicker';
    import vSelect from 'vue-select';
    import {inputHandler} from '../mixins/inputHandler'
    import numeral from 'numeral'

    numeral.localeData().delimiters.thousands = ' ';

    export default {
        name: "BModel",
        mixins: [inputHandler],
        data() {
            return {
                modalShow: false,
                modal: this.$store.getters.getModal,
                model: this.$store.getters.getModalModel,
                add_models: this.$store.getters.getModalAddModels,
                model_name: null,
                block: {},
                request: false,
            }
        },
        components: {
            BModal,
            Zone,
            vSelect,
            Datepicker
        },
        computed: {
            editFields() {
                return this.modal.fields.filter(e => e.edit);
            }
        },
        created() {
            EventBus.$on('showModal', (data) => {
                this.modalShow = true;
                this.modal = data.modal;
                this.model = data.modal.model;
                this.add_models = data.modal.addModels;
                this.model_name = data.modal.model_name;
                this.block = data.modal.block;
                this.request = data.modal.send_request;
            })
        },
        methods: {
            setDefaultValue(field) {
                // let input = document.querySelector('.modal-body input[name="'++'"]')
            },
            inputHandler(event, field, model) {
                let value = numeral(event.target.value);
                let valueString = value.format(field.format).replace(/,/g, ' ')
                model[field.key] = value.value();
                event.target.value = valueString;
                // console.log([...arguments],value)
            },
            customFormatter(date) {
                return this.$moment(date).format('MM-DD-YYYY');
            },
            deepValue(value) {
                return value.hasOwnProperty('value') ? value.value : value;
            },
            handleCancel(e) {
                EventBus.$emit('editFinish', null);
            },
            handleOk(e) {
                e.preventDefault();

                this.checkValidity();

                // this.$store.commit('handleModalOk', {e, root: this.$root})
            },

            /**
             *
             **/
            async checkValidity() {
                for (var item in this.fieldsVV) {
                    this.fieldsVV[item].touched = true;
                }

                var result = await this.$validator.validateAll();
                if (result) {
                    if (this.modal.type === 'editRow' && !this.request) {
                        this.model.status = this.model.id ? 1 : 2;
                        EventBus.$emit('editFinish', this.model);
                        this.$refs['b-modal'].hide();
                        return false;
                    }
                    try {
                        let data = {
                            form_parameters: {
                                form_base_data_model: this.model_name,
                            },
                            main_data_models: {}
                        };
                        this.model.status = 2;
                        data.main_data_models[this.model_name] = [this.model];
                        if (this.modal.type !== 'previewFile') {
                            var res = await this.$http.post(this.modal.link, data);
                            this.$store.dispatch('showToast', {
                                template: res.data.template ? res.data.template : 'success',
                                title: res.data.title,
                                message: res.data.message,
                                position: res.data.position,
                            });
                        }

                        this.$refs['b-modal'].hide();
                        if (this.modal.type === 'editRow') EventBus.$emit('editFinish', this.model);

                        if (this.modal.type !== 'previewFile') this.clearModalValues();
                        EventBus.$emit('triggerModelUpdate');
                    } catch (error) {

                        this.$store.dispatch('showToast', {
                            template: 'error',
                            title: error.response.status,
                            message: error.response.data.message,
                            position: error.response.data.position,
                        })
                    }
                }
            },

            /**
             * очистка модели
             */
            clearModalValues() {
                for (var prop in this.model) {
                    this.model[prop] = '';
                }
            },
        },
        watch: {
            model: {
                handler: function (oldVal, val) {
                    if (val.hasOwnProperty('bl_leasing_object_price') &&
                        val.hasOwnProperty('bl_leasing_object_quantity') &&
                        val.hasOwnProperty('bl_leasing_object_sum'))
                        val['bl_leasing_object_sum'] = val['bl_leasing_object_price'] * val['bl_leasing_object_quantity']
                },
                deep: true
            },
        }

    }
</script>

<style lang="scss">
    @import "../assets/sass/variables";

    .modal-dialog {
        .field-box {
            position: relative;

            .vv-error {
                position: absolute;
                left: 0;
                bottom: -15px;
                display: contents;
            }

            input.invalid {
                border-color: $invalid;
            }

            .v-select.invalid {
                .vs__dropdown-toggle {
                    border-color: $invalid;
                }
            }

            .v-select.valid {
                .vs__dropdown-toggle {
                    border-color: $success;
                }
            }

            .vdp-datepicker.invalid {
                input {
                    border-color: $invalid;
                }
            }

            .vdp-datepicker {
                color: inherit !important;
            }

            .vdp-datepicker.valid {
                input {
                    border-color: $success;
                }
            }

            input.valid {
                border-color: $success;

            }

            .v-select.valid {
                .vs__dropdown-toggle {
                    border-color: $success;
                }
            }

            .vs--single.vs--open .vs__selected {
                margin-top: 7px;
            }
        }

        .img-container {
            display: flex;
            justify-content: center;
        }

        embed {
            width: 100%;
            min-height: 40rem;
        }
    }


</style>
