<template>
  <div class="rate" id="rate">
    <div class="tabs">
      <ul class="tabs__header">
        <li :class="{active: tab_index === 1}" @click="tab_index = 1">{{translate['Table']}}</li>
        <li :class="{active: tab_index === 2}" @click="tab_index = 2">{{translate['List']}}</li>
      </ul>
      <div class="tabs__body" v-bind:style=" tab_index === 1 ? { overflow: 'hidden' } : { } ">
        <template v-if="tab_index === 1">
          <div class="scroll-hidden" ref="container">
            <table>
              <thead>
                <tr>
                  <th>{{translate['Give_1']}}</th>
                  <th>{{translate['Get_1']}}</th>
                </tr>
              </thead>
              <tbody v-if="Object.keys(groupsCurrencies).length">
                <template v-for="(group,indexGroup) in groupsCurrencies">
                  <tr :key="indexGroup">
                    <td class="full">{{indexGroup}}</td>
                  </tr>
                  <tr
                    v-for="(currency, index) in groupsCurrencies[indexGroup]"
                    :key="indexGroup+'_'+index"
                  >
                    <td
                      @click="matchLeft = currency.id"
                      class="half"
                      :class="{active: matchLeft === currency.id}"
                    >{{currency.title}}</td>

                    <td
                      @click="matchRigth = currency.id"
                      class="half"
                      :class="{active: matchRigth === currency.id}"
                    >{{currency.title}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>
          <button
            @click="viewFull"
            class="rate__view-full btn btn-default"
            :class="{reverse: this.fullView}"
          >
            <i class="fa fa-angle-down" aria-hidden="true"></i>
          </button>
        </template>
        <template v-if="tab_index === 2">
          <div class="rate__list">
            <label for="select__from">{{translate['Give_1']}}</label>
            <v-select
              label="title"
              v-model="matchLeft"
              item-value="id"
              :reduce="option => option.id"
              :options="groupsCurrenciesList"
            ></v-select>
            <br />
            <label for="select__from">{{translate['Get_1']}}</label>
            <v-select
              label="title"
              v-model="matchRigth"
              item-value="id"
              :reduce="option => option.id"
              :options="groupsCurrenciesList"
            ></v-select>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import vSelect from "vue-select";
import { EventBus } from "../main";
export default {
  name: "Rate",
  data() {
    return {
      tab_index: 1,
      matchLeft: this.$store.getters.getMatchCurrencies.left,
      matchRigth: this.$store.getters.getMatchCurrencies.right,
      groupsCurrencies: [],
      groupsCurrenciesList: [],
      fullView: false,
      translate: [],
    };
  },
  components: {
    vSelect
  },
  async mounted() {
    this.groupsCurrencies = await this.getGroupsCurrencies();
    let items = Object.keys(this.groupsCurrencies)
      .map(e => Object.values(this.groupsCurrencies[e]))
      .flat();
    this.$store.dispatch("updateCurrencies", items);
    this.groupsCurrenciesList = items;
  },
  watch: {
    matchLeft() {
      this.matchHandler();
    },
    matchRigth() {
      this.matchHandler();
    }
  },
  methods: {
    viewFull() {
      let container = this.$refs.container;
      let countCurr = this.groupsCurrenciesList.length;
      let heightRate = countCurr * 31;
      if (this.fullView) {
        container.style.maxHeight = "900px";
        this.fullView = false;
      } else {
        container.style.maxHeight = heightRate + "px";
        this.fullView = true;
      }
    },
    matchHandler(currency, position) {
      if (this.matchLeft && this.matchRigth) {
        let data = { left: this.matchLeft, right: this.matchRigth };
        this.$store.dispatch("refreshMatch", data);
        EventBus.$emit("getOffers", data);
        this.$bRouter.push("offers");
      }
    },

    async getGroupsCurrencies() {
      let groups = {};

      const RES = await this.$http.post("/get/payment/methods/groups");

      function compare(a, b) {
        const bandA = a.order;
        const bandB = b.order;

        let comparison = 0;
        if (bandA > bandB) {
          comparison = 1;
        } else if (bandA < bandB) {
          comparison = -1;
        }
        return comparison;
      }
      this.translate = RES.data.translated;

      Object.keys(RES.data.groups[0])
        .map(i => RES.data.groups[0][i])
        .sort(compare)
        .forEach((group, index) => {
          delete group.order;
          groups[Object.keys(RES.data.groups[0])[index]] = group;
        });

      return groups;
    }
  }
};
</script>

